import {
	queryBaseActionHandlers,
	queryBaseFetchDataEffectHandler
} from '@devsnc/sn-glide-query-base';

let query = `
query (
  $chars: String!, $paginationLimit: Int, $paginationOffset: Int,
  $shortList: Boolean,
  $canRead: Boolean,
  $includeDefault: Boolean,
  $selectedOnly: Boolean,
  $selectedField: String,
  $selected: String,
  $noViews: Boolean,
  $noSystemTables: Boolean,
  $filterEntitledTables: Boolean,
  $tableName: String,
  $fieldName: String,
  $showTableNames: Boolean,
  $showTableNamesOnLabel: Boolean,
  $tableChoicesScript: String,
  $skipRoot: Boolean,
  $baseTable: String,
  $recordSysId: String!,
  $serializedChanges: String!,
  $encodedRecord: String!
  ) {
  GlideLayout_Query {
    tableChoiceListRetriever(
      chars: $chars,
      pagination: {limit: $paginationLimit, offset: $paginationOffset},
      shortList: $shortList,
      canRead: $canRead,
      includeDefault: $includeDefault,
      selectedOnly: $selectedOnly,
      selectedField: $selectedField,
      selected: $selected,
      noViews: $noViews,
      noSystemTables: $noSystemTables,
      filterEntitledTables: $filterEntitledTables,
      tableName: $tableName,
      fieldName: $fieldName,
      showTableNames:$showTableNames,
      showTableNamesOnLabel: $showTableNamesOnLabel,
      tableChoicesScript:$tableChoicesScript,
      skipRoot:$skipRoot,
      baseTable:$baseTable,
      recordSysId:$recordSysId,
      serializedChanges:$serializedChanges,
      encodedRecord:$encodedRecord
    ) {
      tableNames {
        value
        displayValue
      }
    }
  }
}
`;

let variableList = [
	'chars',
	'paginationLimit',
	'paginationOffset',
	'shortList',
	'canRead',
	'includeDefault',
	'selectedOnly',
	'selectedField',
	'selected',
	'noViews',
	'noSystemTables',
	'filterEntitledTables',
	'tableName',
	'fieldName',
	'showTableNames',
	'showTableNamesOnLabel',
	'tableChoicesScript',
	'skipRoot',
	'baseTable',
	'recordSysId',
	'serializedChanges',
	'encodedRecord'
];

const queryBaseActionHandlersData = queryBaseActionHandlers(
	query,
	variableList
);
export const queryBaseFetchDataEffect = queryBaseFetchDataEffectHandler(
	query,
	variableList
);
export default queryBaseActionHandlersData;
