import { omit, get, map } from 'lodash';

import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import {
	getEncodedRecord,
	getSerializedChanges
} from '@devsnc/sn-controls-common';

import actionHandlers from './actionHandlers';
import { adaptCallbackForLegacyAPI } from '@devsnc/sn-controls-common';
import { FETCH_DATA } from '@devsnc/sn-glide-query-base';

const { COMPONENT_PROPERTY_CHANGED } = actionTypes;

const view = (state, { dispatch, updateProperties, updateState }) => {

	const mapResults = data => {
		let results = get(data, 'GlideLayout_Query.tableChoiceListRetriever', {});
		return {
			options: map(results.tableNames, t => ({
				...t,
				secondary: [t.value]
			}))
		};
	};
	
	const getVariables = inputVal => {
		const {
			name,
			tableName,
			value,
			resultLimit,
			dictionaryAttributes,
			formData = {},
			referringRecordId
		} = state.properties;

		return {
			paginationLimit: resultLimit,
			chars: inputVal.length > 0 ? inputVal : '**',
			shortList: dictionaryAttributes.shortList,
			canRead: dictionaryAttributes.canRead,
			includeDefault: dictionaryAttributes.includeDefault,
			selectedOnly: dictionaryAttributes.selectedOnly,
			noViews: dictionaryAttributes.noViews,
			noSystemTables: dictionaryAttributes.noSystemTables,
			filterEntitledTables: dictionaryAttributes.filterEntitledTables,
			skipScopeRestrictions: dictionaryAttributes.skipScopeRestrictions,
			tableName: tableName,
			fieldName: name,
			selectedField: `${tableName}.${name}`,
			selected: value || '',
			showTableNames: dictionaryAttributes.showTableNames,
			showTableNamesOnLabel: dictionaryAttributes.showTableNamesOnLabel,
			tableChoicesScript: dictionaryAttributes.tableChoicesScript,
			skipRoot: dictionaryAttributes.skipRoot,
			baseTable: dictionaryAttributes.baseTable,
			recordSysId: referringRecordId,
			serializedChanges: getSerializedChanges(state.properties),
			encodedRecord: getEncodedRecord(formData)
		};
	};

	const {
		results,
		isSearching,
		searchUsingStartsWith,
		searchOnClick,
		liveUpdate,
		value
	} = state.properties;

	if (liveUpdate && state.liveUpdate) {
		//reset the liveUpdate prop
		updateProperties({
			liveUpdate: false
		});

		if (value) {
			const payload = {
				...getVariables(value),
				chars: value,
				paginationLimit: 1
			};
			dispatch(FETCH_DATA, payload, { stopPropagation: true });
		} else {
			updateState({ liveUpdate: false });
		}
	}

	const additionalProps = omit(state.properties, ['resultLimit']);

	return (
		<sn-glide-query-base
			{...additionalProps}
			isSearching={isSearching}
			results={results} // results was created from createUpdateStateEffect
			mapResults={mapResults}
			getVariables={getVariables}
			searchUsingStartsWith={searchUsingStartsWith}
			searchOnClick={searchOnClick}
			onValueChange={adaptCallbackForLegacyAPI(state.properties.onValueChange)}
			includeHeaders={false}
		/>
	);
};

const ConnectedTableName = 'sn-record-tablename-connected';

createCustomElement(ConnectedTableName, {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {
		liveUpdate: false
	},
	actionHandlers: {
		...actionHandlers,
		[COMPONENT_PROPERTY_CHANGED]: ({
			action,
			state,
			updateProperties,
			updateState
		}) => {
			const { name, value } = action.payload;

			if (name === 'liveUpdate' && value === true) {
				//use state liveUpdate as the prop updates comes in random order
				updateState({
					liveUpdate: true
				});
			} else if (state.liveUpdate && name === 'results') {
				let data = get(
					value,
					'GlideLayout_Query.tableChoiceListRetriever.tableNames[0]'
				);
				updateProperties({
					displayValue: data.displayValue,
					liveUpdate: false
				});
				updateState({
					liveUpdate: false
				});
			}
		}
	},
	properties: {
		/**
		 * The field name
		 * @type {string}
		 */
		name: { schema: { type: 'string' } },

		/**
		 * Name of table this field is on
		 * @type {string}
		 */
		tableName: { schema: { type: 'string' } },

		/**
		 * Dictionary attributes to set and configure the query for table names
		 */
		dictionaryAttributes: { schema: { type: 'object' } },

		/**
		 * Name for the component.
		 * @type {string}
		 */
		label: {},

		/**
		 * Additional content describing the field, usually to provide further
		 * instruction to the user.
		 * @type {string}
		 */
		description: {},

		/**
		 * Flag to show when a graphQL request is in progress
		 */
		isSearching: {},

		/**
		 * Value for current omponent.
		 * @type {string}
		 */
		value: {},

		/**
		 * DisplayValue for current component.
		 * @type {string}
		 */
		displayValue: {},

		/**
		 * results from a graphQL request
		 */
		results: {
			schema: { type: 'object' }
		},

		/**
		 * Integer limit of choice suggestions
		 */
		resultLimit: { default: 25 },

		/**
		 * Callbacks for Forms consumption
		 * @type {function}
		 * @private
		 */
		onValueChange: {},

		/**
		 * Content to display when there is no suitable display value.
		 * @type {string}
		 */
		placeholder: {},

		/**
		 * Flag to Enable "contains" searches for reference autocompleters
		 */
		searchUsingStartsWith: { default: true },

		/**
		 * Flag to searching when clicking on field
		 */
		searchOnClick: { default: true },

		/**
		 * Flag to toggle turning off display value warning
		 */
		disableDisplayValueWarning: { default: false },

		/**
		 * Specifies whether the current value is not valid.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true. Used by the glide form controls demo
		 * @type {string | boolean}
		 * @private
		 */
		invalid: {},

		/**
		 * Specifies that the user cannot modify the value of the component.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 * @type {string | boolean}
		 */
		readonly: { default: false },
		/**
		 * Whether or not the user must fill in a value for this component.
		 * Can be specified as a boolean or the string "true"
		 * @type {string | boolean}
		 */
		required: { default: false },
		/**
		 * Specifies that the field should be rendered right-to-left.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 * @type {string | boolean}
		 * @private
		 */
		reversed: { default: false },

		/**
		 * Array of message objects that must contain type and message itself.
		 *
		 * @type {Array<{type: ("error"|"warning"|"success"|"liveUpdate"|"info"|"suggestion"), type:Array<string,Object<{string, getMessage:function}>> }>}
		 */
		messages: {},

		/**
		 * Flag to indicate if it's a liveUpdate
		 */
		liveUpdate: {
			schema: { type: 'boolean' }
		},

		/**
		 * This is the Sys ID of the record that has the field in it.
		 */
		referringRecordId: { schema: { type: 'string' } }
	}
});

export default ConnectedTableName;
