/* eslint-disable no-console */

import React from 'react';
import * as FIELD_TYPE from './FieldType';
import {
	memoize,
	get,
	includes,
	isNil,
	pickBy,
	identity,
	chain,
	omit,
	isArray,
	has,
	isNumber,
	map,
	isEmpty,
	find,
	some,
	flow
} from 'lodash';
import { t } from 'sn-translate';
import { LiveUpdateMessage } from '@devsnc/sn-form-controls';
import fixLocale from './../common/utils/fixLocale';
import toHtml from './../common/utils/toHtml';
import { getUserLanguage, resolveLanguage } from './../common/utils/language';
import { canRenderFieldAction } from '@devsnc/sn-declarative-actions';
import {
	getDeclarativeActionsProps,
	escapeSeismicSpecialSymbols,
	isAttrTrue,
	getProperty
} from '@devsnc/sn-controls-common';
import moment from 'moment';

const ENCRYPTED_TEXT_PROPS = {
	indicator: ' (' + t('Encrypted') + ')'
};

export function isSupported(type) {
	return includes(FIELD_TYPE, type);
}

// TODO: use sn-workspace-header prefetched data source and remove this function
const getCurrentUser = formData => {
	return (formData.classicForm
		? memoize(() =>
				get(
					window,
					'ux_globals.presource["sn-workspace-header:wsUserData"].data.GlideDomain_Query.user',
					{}
				)
		  )
		: () =>
				get(formData, 'userSession', {
					countryCallingCode: '',
					dateFormat: 'yyyy-MM-dd',
					dateTimeFormat: 'yyyy-MM-dd HH:mm:ss',
					language: 'en',
					timeFormat: 'HH:mm:ss',
					timeZone: 'US/Pacific',
					timeZoneOffset: -28800000
				}))();
};

// Sad caching so we don't need to recreate the `toolbar` array on every
// render.
let tinymceToolbar = [];

function getTinyMCEToolbar() {
	const line1 = getProperty('glide.ui.html.editor.v4.toolbar.line1');
	const line2 = getProperty('glide.ui.html.editor.v4.toolbar.line2');

	if (isNil(line1) && isNil(line2)) return undefined;

	if (line1 !== tinymceToolbar[0] || line2 !== tinymceToolbar[1]) {
		tinymceToolbar = [line1, line2];
	}

	return tinymceToolbar;
}

function getHtmlAttributes(dictionaryAttributes, type) {
	//map the attributes to our prop names & return truthy properties only
	return (
		dictionaryAttributes &&
		pickBy(
			{
				height: dictionaryAttributes['editor.height'],
				maxHeight: dictionaryAttributes['editor.max_height'],
				plugins: dictionaryAttributes['editor.plugins'],
				toolbar: dictionaryAttributes['editor.toolbar'],
				browserSpellcheck: dictionaryAttributes['editor.spellcheck']
			},
			identity
		)
	);
}

function getDictionaryAttrs(controlProps) {
	return chain(get(controlProps, 'dictionary.attributes'))
		.keyBy('name')
		.mapValues('value')
		.value();
}

/**
 * For date/time fields, `displayValue` is adjusted for the user's timezone
 * pref, `value` is not. Move the displayValue over to the value prop so that
 * the form control can deal with localized time rather than UTC.
 */
function adjustPropsForTimezone(controlProps) {
	const displayValue = controlProps.displayValue;
	if (!displayValue) {
		return controlProps;
	}

	const updatedProps = omit(controlProps, 'displayValue');
	updatedProps.value = displayValue;

	return updatedProps;
}

/**
 * For duration fields, the value received in the liveUpdate will always be in
 * the format 'YYYY-MM-DD hh:mm:ss' (regardless of user system property preferences
 * changing format of date and/or time), and the base value is '1970-01-01 00:00:00'.
 * This function is made for the purpose of changing the value to the format that
 * the duration field expects, which happens to be 'd hh:mm:ss'.
 */

export function normalizeDurationValue(value) {
	const dateValue = moment(value, 'YYYY-MM-DD hh:mm:ss', true);

	if (dateValue.isValid()) {
		const days = dateValue.diff(moment('1970-01-01'), 'days');
		value = `${days} ${dateValue.format('hh:mm:ss').toString()}`;
	}

	return value;
}

function getFirstDayOfTheWeek() {
	let firstDayOfWeek = Number.parseInt(
		getProperty('glide.ui.date_picker.first_day_of_week'),
		10
	);
	return !Number.isNaN(firstDayOfWeek) ? firstDayOfWeek : undefined;
}

export function getMessagesAndLiveUpdate(liveUpdate, msgs) {
	const messages = isArray(msgs) ? msgs : [];

	if (liveUpdate) {
		let userName = get(liveUpdate, 'updatedBy.displayName');
		let newMsg = { type: '', message: new LiveUpdateMessage(userName) };
		if (liveUpdate.isUserModified) {
			newMsg.type = 'warning';
			newMsg.message.setNewValue(liveUpdate.displayValue);
		} else {
			newMsg.type = 'liveUpdate';
		}

		return [...messages, newMsg];
	}
	return messages;
}

/**
 * All stateful components use `initialValue` instead of `value`
 */
export function setInitialValue(controlProps, useDisplayValue) {
	const hasValue = has(controlProps, 'value');
	const hasDisplayValue = has(controlProps, 'displayValue');

	if (!hasValue && !hasDisplayValue) {
		return controlProps;
	}

	const newControlProps = omit(controlProps, ['value', 'displayValue']);

	if (hasValue) {
		newControlProps.initialValue = useDisplayValue
			? escapeSeismicSpecialSymbols(controlProps.displayValue)
			: escapeSeismicSpecialSymbols(controlProps.value);
	}

	if (hasDisplayValue) {
		newControlProps.initialDisplayValue = escapeSeismicSpecialSymbols(
			controlProps.displayValue
		);
	}

	return newControlProps;
}

function controlOrSysProp(defaultValue, sysProp, controlPropValue) {
	let value = defaultValue;
	const sysPropValue = getProperty(sysProp);
	if (sysPropValue !== undefined && sysPropValue !== null) value = sysPropValue;
	if (controlPropValue !== undefined && controlPropValue !== null)
		value = controlPropValue;
	return value;
}

const userMention = {
	'@': 'users'
};

const sanitizeField = field => {
	// Remove props that can conflict with HTML or have otherwise unintended side effects
	// eslint-disable-next-line no-unused-vars
	const { type, hidden, ...validAttrs } = field;

	if (hidden) {
		console.warn(
			`Invalid attribute "hidden" found for ${field.name}. Use "visible" instead.`
		);
	}

	return validAttrs;
};

const getTimezoneOffset = user => {
	let millisecondsToHours = ms => ms / 1000 / 60 / 60;

	return isNumber(user.timeZoneOffset)
		? millisecondsToHours(user.timeZoneOffset)
		: undefined;
};

const getTimeProps = controlProps => {
	const { formData } = controlProps;

	return adjustPropsForTimezone({
		...controlProps,
		firstDayOfWeek: getFirstDayOfTheWeek(),
		language: getUserLanguage(getCurrentUser(formData)),
		utcOffset: getTimezoneOffset(getCurrentUser(formData))
	});
};

const getReferenceProps = controlProps => ({
	...controlProps,
	tableName: controlProps.referringTable,
	fieldName: controlProps.name,
	referenceTable: controlProps.reference,
	displayValue: escapeSeismicSpecialSymbols(controlProps.displayValue),
	recordSysId: controlProps.referringRecordId,
	searchCancelable: getProperty(
		'glide.request_manager.cancel_reference_completer',
		true
	),
	maxSearchMatches: Number.parseInt(
		getProperty('glide.ui.max_search_matches', 250),
		10
	)
});

const getControlPropsForUnSupported = (controlProps, type) => ({
	...controlProps,
	readonly: true,
	description: t('* Field type {0} is not supported.', type)
});

const getControlPropsForTextArea = (controlProps, useDisplayValue) => {
	return setInitialValue(
		{
			...controlProps,
			type: 'textarea',
			showCounter: isAttrTrue(
				controlOrSysProp(
					false,
					'glide.ui.textarea.character_counter',
					controlProps.showCounter
				)
			)
		},
		useDisplayValue
	);
};

const getFallbackCurrencyDisplay = (code, symbol, reversed) =>
	code === symbol || !symbol
		? code
		: reversed
		? `(${symbol}) ${code}`
		: `${code} (${symbol})`;

const normalizeCurrencyCodes = (currencyCodes, reversed) =>
	map(currencyCodes, ({ code, symbol }) => ({
		value: code,
		displayValue: getFallbackCurrencyDisplay(code, symbol, reversed)
	}));

const normalizeCountryCodes = countryCodes =>
	map(countryCodes, code => ({
		...code,
		value: code.code,
		displayValue: `${code.name} +${code.code}`
	}));

const normalizeCurrencyValue = controlProps => {
	if (controlProps.invalid) {
		return controlProps.value;
	}

	// The proper value is put in liveUpdate for live updates
	if (has(controlProps, 'liveUpdate.displayString')) {
		return get(controlProps, 'liveUpdate.displayString');
	}

	// Won't work currently but we can update glide to provide this in the future
	if (controlProps.displayString) {
		return controlProps.displayString;
	}

	const { defaultCurrency, code, value } = controlProps;
	if (isEmpty(value) || !code) {
		return value;
	}

	const currentCode = defaultCurrency || code;

	// check if code has already been prepended
	return includes(value, ';') ? value : `${currentCode};${value}`;
};

const getDateFormat = formData =>
	memoize(() =>
		(getCurrentUser(formData).dateFormat || 'YYYY-MM-DD').toUpperCase()
	)();

const getTimeFormat = formData =>
	memoize(() => getCurrentUser(formData).timeFormat || 'HH:mm:ss')();

const getDateTimeFormat = formData =>
	memoize(() => `${getDateFormat(formData)} ${getTimeFormat(formData)}`)();

const getAttribute = controlProps => (attribute, defaultValue) => {
	const attributes = get(controlProps, 'dictionary.attributes', []);

	const attributeValue = find(attributes, a => a.name === attribute);

	return attributeValue === undefined ? defaultValue : attributeValue.value;
};

const checkIfInfoMessageExists = controlProps => {
	const { dependentFieldLabel, messages } = controlProps;
	const infoMessage = t(
		`Select a ${dependentFieldLabel} before modifying this field`
	);
	return messages.find(item => item.message === infoMessage);
};

const getFieldNameProps = controlProps => {
	const {
		dependentValue,
		dependentFieldLabel,
		readonly,
		displayValue
	} = controlProps;
	if (
		isEmpty(dependentValue) &&
		!isAttrTrue(readonly) &&
		isEmpty(displayValue) &&
		!checkIfInfoMessageExists(controlProps)
	) {
		return {
			...controlProps,
			messages: [
				...controlProps.messages,
				{
					type: 'info',
					message: t(
						`Select a ${dependentFieldLabel} before modifying this field`
					)
				}
			],
			isReadOnly: true
		};
	}
	return controlProps;
};

export function createFormControl(
	parentId,
	type,
	field,
	onValueChange,
	onStagedValueChange,
	formData
) {
	const { Control, props, children, render } = getFormControl(
		parentId,
		type,
		field,
		onValueChange,
		onStagedValueChange,
		formData
	);

	if (!render) {
		return null;
	}

	return <Control {...props}>{children}</Control>;
}

export function getFormControl(
	parentId,
	type,
	field,
	onValueChange,
	onStagedValueChange,
	formData
) {
	if (field.visible === false || field.visible === 'false')
		return { render: false };

	const componentId = `${parentId}_${type}_${field.name}`;
	const messages = getMessagesAndLiveUpdate(field.liveUpdate, field.messages);
	const declarativeActionsProps = getDeclarativeActionsProps(field);
	let controlProps = sanitizeField({
		...field,
		context: 'form',
		key: componentId,
		'component-id': componentId,
		maxlength: field.maxLength,
		invalid:
			field.isInvalid || field.invalid || some(messages, { type: 'error' }),
		required: field.mandatory,
		messages,
		onValueChange: onValueChange,
		onStagedValueChange: onStagedValueChange,
		fieldType: type, // we need field type, but it is stripped out so put it under a different key that it won't have html side effects
		formData
	});

	let Control, controlChildren, singleCurrency;
	let useAllFractionsDigits = true;
	let maxFractionDigits = 12; // max number of fractions displayed for FX Currency

	switch (type) {
		case FIELD_TYPE.BOOLEAN:
			Control = 'now-record-checkbox';
			break;

		case FIELD_TYPE.CHOICE:
			Control = 'sn-record-choice-connected';
			controlProps = setInitialValue({
				...controlProps,
				'table-name': field.referringTable,
				'record-sys-id': field.referringRecordId,
				optionStack: field.optionStack,
				dictionaryAttributes: {
					refQualElements: getAttribute(controlProps)('ref_qual_elements', '')
				}
			});
			break;

		case FIELD_TYPE.CURRENCY:
			singleCurrency = isAttrTrue(
				getProperty('glide.i18n.single_currency', false)
			);
			useAllFractionsDigits = isAttrTrue(
				getProperty('glide.currency_price.use_all_fraction_digits', false)
			);

			maxFractionDigits = 4; // max fraction digits for Currency field

		case FIELD_TYPE.CURRENCY2:
			Control = 'sn-record-currency-connected';
			// display_string is the curr;value, i.e. JPY;1000
			// value is the converted number i.e. 9.07  (but in the default system currency, in this case USD)
			// displayValue is the string with the converted number i.e. "$9.07"
			// so, use display_string and default to value
			controlProps = setInitialValue({
				singleCurrency,
				...controlProps,
				useAllFractionsDigits,
				maxFractionDigits,
				currencyCodes: normalizeCurrencyCodes(
					controlProps.currencyCodes,
					controlProps.reversed
				),
				value: normalizeCurrencyValue(controlProps),
				locale: fixLocale(getProperty('glide.system.locale'))
			});
			break;

		case FIELD_TYPE.DATE_TIME: {
			Control = 'now-record-date-picker';
			controlProps = getTimeProps({
				...controlProps,
				format: getDateTimeFormat(formData),

				timePicker: true,
				declarativeActionsProps
			});

			break;
		}

		case FIELD_TYPE.DATE: {
			Control = 'now-record-date-picker';

			controlProps = getTimeProps({
				...controlProps,
				format: getDateFormat(formData),

				declarativeActionsProps
			});

			break;
		}

		case FIELD_TYPE.TIME:
		case FIELD_TYPE.UTC_TIME: {
			Control = 'now-record-time';

			let format = controlProps.format || getTimeFormat(formData);

			const separator = format.includes('.') ? '.' : ':';

			if (type === FIELD_TYPE.UTC_TIME) {
				const isUsingUserFormat = chain(controlProps)
					.get('dictionary.attributes', [])
					.some(
						attr => attr.name === 'user_time_format' && isAttrTrue(attr.value)
					)
					.value();

				format = isUsingUserFormat ? format : 'HH:mm:ss';
			}

			// `aa` is a legal in Java (supported by SN) but not for moment js.
			format = format.replace('aa', 'a');

			controlProps = adjustPropsForTimezone({
				...controlProps,
				separator,
				format
			});
			break;
		}

		case FIELD_TYPE.PASSWORD:
			Control = 'sn-record-input-connected';
			controlProps = setInitialValue({
				...controlProps,
				type: 'password',
				declarativeActionsProps
			});
			break;

		case FIELD_TYPE.EMAIL:
			Control = 'sn-record-email';
			controlProps = setInitialValue({
				...controlProps,
				declarativeActionsProps
			});
			break;

		case FIELD_TYPE.PHONE:
			const formatUsPhone = isAttrTrue(
				getProperty('glide.ui.format_phone', true)
			);
			if (!formatUsPhone) {
				Control = 'sn-record-input-connected';
				break;
			}
		case FIELD_TYPE.PHONE_NUMBER:
		case FIELD_TYPE.PHONE_NUMBER_E164:
			Control = 'sn-record-phone-connected';
			controlProps = setInitialValue({
				...controlProps,
				countryCode: controlProps.country,
				phoneAddon: canRenderFieldAction({
					model: controlProps,
					action: {
						dependency: 'CTI',
						requiresExistingRecord: true
					},
					formData
				}),
				countryCodes: normalizeCountryCodes(controlProps.countryCodes),
				formatUsPhone: isAttrTrue(getProperty('glide.ui.format_phone', true)),
				declarativeActionsProps
			});
			break;

		case FIELD_TYPE.REFERENCE: {
			Control = 'sn-record-reference-connected';

			let resultLimit = Number.parseInt(
				getProperty('glide.ui.max_ref_dropdown'),
				10
			);
			if (Number.isNaN(resultLimit)) resultLimit = undefined;

			controlProps = getReferenceProps({
				...controlProps,
				referenceAddonReadonly: isAttrTrue(
					controlOrSysProp(
						false,
						'glide.ui.reference.readonly.clickthrough',
						field.readonly_clickthrough
					)
				),
				resultLimit,
				disableDisplayValueWarning: isAttrTrue(
					getProperty('glide.ui.reference.disable_displayvalue_warning', true)
				),
				declarativeActionsProps,
				searchUsingStartsWith: isAttrTrue(
					getProperty('glide.ui.ref_ac.startswith', true)
				),
				searchOnClick: isAttrTrue(
					getAttribute(controlProps)('ref_search_on_click', true)
				)
			});
			break;
		}

		case FIELD_TYPE.TABLE_NAME: {
			Control = 'sn-record-tablename-connected';

			const getAttr = getAttribute(controlProps);

			controlProps = {
				...controlProps,
				tableName: controlProps.referringTable,
				liveUpdate: controlProps.liveUpdate ? true : false,
				dictionaryAttributes: {
					shortList: !isAttrTrue(getAttr('full_list'), true),
					canRead: isAttrTrue(getAttr('can_read_table')),
					includeDefault: isAttrTrue(getAttr('includeDefault')),
					selectedOnly: isAttrTrue(getAttr('selected_only')),
					noViews: isAttrTrue(getAttr('no_views')),
					noSystemTables: isAttrTrue(getAttr('no_system_tables')),
					filterEntitledTables: isAttrTrue(
						getAttr('filter_licensed_custom_tables')
					),
					showTableNames: isAttrTrue(getAttr('show_table_names')),
					showTableNamesOnLabel: isAttrTrue(
						getAttr('show_table_names_on_label')
					),
					tableChoicesScript: getAttr('tableChoicesScript'),
					skipRoot: isAttrTrue(getAttr('skip_root')),
					baseTable: getAttr('base_table')
				},
				formData
			};

			break;
		}

		case FIELD_TYPE.FIELD_NAME: {
			Control = 'sn-record-fieldname-connected';
			const getAttr = getAttribute(controlProps);
			controlProps = getFieldNameProps({
				...controlProps,
				liveUpdate: controlProps.liveUpdate ? true : false,
				displayValue: escapeSeismicSpecialSymbols(controlProps.displayValue),
				dictionaryAttributes: {
					referenceTypes: getAttr('reference_types'),
					allowDotWalking: isAttrTrue(getAttr('allow_references'), false)
				},
				dependentFieldValue:
					controlProps.dependentFieldValue || controlProps.dependentValue
			});
			break;
		}

		case FIELD_TYPE.GLIDE_LIST: {
			Control = 'sn-record-reference-connected';

			let resultLimit = Number.parseInt(
				getProperty('glide.ui.max_ref_dropdown'),
				10
			);
			if (Number.isNaN(resultLimit)) resultLimit = undefined;

			controlProps = getReferenceProps({
				...controlProps,
				displayValue: field.display_value_list,
				'form-data': formData,
				resultLimit,
				multiSelection: true,
				disableDisplayValueWarning: isAttrTrue(
					getProperty('glide.ui.reference.disable_displayvalue_warning', true)
				),
				declarativeActionsProps
			});
			break;
		}

		case FIELD_TYPE.FIELD_LOOKUP: {
			Control = 'now-record-field-lookup-connected';
			break;
		}

		case FIELD_TYPE.DOCUMENT_ID:
			Control = 'sn-record-document-id-connected';
			const prepareDeclarativeActions = controlProps => ({
				...controlProps,
				declarativeActionsProps: {
					...controlProps.declarativeActionsProps,
					reference: controlProps.dependentValue,
					referenceTable: controlProps.dependentValue,
					ignoreDepAsRefQual: true
				}
			});
			controlProps = flow(
				getReferenceProps,
				prepareDeclarativeActions
			)({
				...controlProps,
				searchAddon: false,
				referenceAddonReadonly: true,
				declarativeActionsProps,
				dependentFieldLabel: get(controlProps, 'dictionary.dependentFieldLabel')
			});
			break;

		case FIELD_TYPE.ENCRYPTED_TEXT:
			if (
				isAttrTrue(getProperty('glide.ui.record.encrypted_text.enabled', false))
			) {
				Control = 'sn-record-input-connected';
				controlProps = {
					...getControlPropsForTextArea(controlProps, true),
					label: controlProps.label + ENCRYPTED_TEXT_PROPS.indicator
				};
			} else {
				Control = 'sn-record-input';
				controlProps = getControlPropsForUnSupported(controlProps, type);
			}
			break;

		case FIELD_TYPE.STRING_LONG:
		case FIELD_TYPE.TEXTAREA:
		case FIELD_TYPE.MULTI_TWO_LINES:
			Control = 'sn-record-input-connected';
			controlProps = getControlPropsForTextArea(controlProps);
			break;

		case FIELD_TYPE.STRING: {
			const isLargeField = Number.parseInt(controlProps.maxlength) > 255;

			Control = 'sn-record-input-connected';
			controlProps = setInitialValue({
				...controlProps,
				type: isLargeField ? 'textarea' : 'text',
				showCounter: isAttrTrue(
					controlOrSysProp(
						false,
						'glide.ui.textarea.character_counter',
						isLargeField && controlProps.showCounter
					)
				),
				declarativeActionsProps
			});
			break;
		}

		case FIELD_TYPE.WORKFLOW:
		case FIELD_TYPE.STRING_SHORT:
			Control = 'sn-record-input-connected';
			controlProps = setInitialValue({
				...controlProps,
				type: 'text',
				declarativeActionsProps
			});
			break;

		case FIELD_TYPE.INTEGER:
			Control = 'now-record-number';
			controlProps = setInitialValue({
				...controlProps,
				decimal: false,
				locale: fixLocale(getProperty('glide.system.locale')),
				format: getDictionaryAttrs(controlProps)['format'],
				declarativeActionsProps
			});
			break;
		case FIELD_TYPE.FLOAT:
		case FIELD_TYPE.DECIMAL:
			Control = 'now-record-number';
			controlProps = setInitialValue({
				...controlProps,
				locale: fixLocale(getProperty('glide.system.locale')),
				format: getDictionaryAttrs(controlProps)['format'],
				declarativeActionsProps
			});
			break;

		case FIELD_TYPE.PERCENT_COMPLETE:
			Control = 'now-record-number';
			controlProps = setInitialValue({
				...controlProps,
				percent: true,
				locale: fixLocale(getProperty('glide.system.locale')),
				format: getDictionaryAttrs(controlProps)['format'],
				declarativeActionsProps
			});
			break;

		case FIELD_TYPE.INPUT_GROUP:
			Control = 'sn-record-input-group';
			break;

		case FIELD_TYPE.URL:
			Control = 'sn-record-url';
			controlProps = setInitialValue(controlProps);
			break;

		case FIELD_TYPE.JOURNAL:
			const useHTML = isAttrTrue(
				getProperty('glide.ui.journal.use_html', false)
			);

			if (!useHTML) {
				Control = 'sn-record-input-connected';
				controlProps = setInitialValue({
					...omit(controlProps, ['maxlength', 'maxLength']),
					mentions: userMention,
					type: 'textarea',
					showCounter: isAttrTrue(
						controlOrSysProp(
							false,
							'glide.ui.textarea.character_counter',
							controlProps.showCounter
						)
					)
				});
				break;
			}

			controlProps.enableMentions = isAttrTrue(
				getProperty('glide.ui.journal.enable_mentions', true)
			);

			controlProps.useCodeBlocks = true;
			controlProps.resizable = true;

			if (controlProps.name === 'work_notes') {
				controlProps.contentStyle = 'body { background-color: #fefaf3; }';
			}
			const systemLanguage = getProperty('glide.sys.language');
			const supportedLanguages = getProperty(
				'glide.ui.html.editor.langauges',
				''
			);

			const userLanguage = getUserLanguage(getCurrentUser(formData));

			controlProps = {
				...controlProps,
				language: resolveLanguage(
					userLanguage,
					systemLanguage,
					supportedLanguages
				),
				toolbar: [
					'bold,italic,underline,|,fontselect,|,alignleft,aligncenter,alignright,|,bullist,numlist,|,link,unlink'
				],
				plugins:
					'link,lists,advlist,table,powerpaste,searchreplace,preview,fullscreen,a11y_fixes,placeholder,sn_resizer',
				validPlugins: '',
				validButtons: '',
				minLineCount: 3,
				fonts: getProperty('glide.ui.html.editor.v4.font.collection'),
				removeHost: isAttrTrue(getProperty('glide.ui.html.editor.remove_host')),
				relativeUrls: isAttrTrue(
					getProperty('glide.ui.html.editor.relative_urls')
				),
				convertUrls: isAttrTrue(
					getProperty('glide.ui.html.editor.convert_urls')
				),
				extendedValidElements: getProperty(
					'glide.ui.html.editor.extended_valid_elements'
				),
				isJournal: true,
				configMentions: {
					enableMentions: controlProps.enableMentions,
					resourceTable: field.referringTable,
					resourceSysId: field.referringRecordId
				}
			};
			Control = 'now-record-html-editor';
			break;

		case FIELD_TYPE.HTML:
		case FIELD_TYPE.TRANSLATED_HTML: {
			const systemLanguage = getProperty('glide.sys.language');
			// This property is actually spelled this way and is not a typo
			const supportedLanguages = getProperty(
				'glide.ui.html.editor.langauges',
				'cs,de,en,es,fi,fr,fq,he,hu,it,ja,ko,nl,pl,pt,ru,th,tr,zh,zt'
			);

			const userLanguage = getUserLanguage(getCurrentUser(formData));

			//make object array to single object with name as key
			const dictionaryAttributes = getDictionaryAttrs(controlProps);
			const htmlAttributes = getHtmlAttributes(
				dictionaryAttributes,
				controlProps.fieldType
			);

			if (controlProps.referringTable === 'kb_knowledge') {
				const knowledgeBlockStyles =
					"body .kb-block-content-item[contentEditable='false'][data-mce-selected]" +
					' { background: #d9ebee; outline: 2px solid #01778e;}';
				controlProps.contentStyle = controlProps.contentStyle
					? `${controlProps.contentStyle} ${knowledgeBlockStyles}`
					: knowledgeBlockStyles;
			}

			controlProps = {
				...controlProps,
				language: resolveLanguage(
					userLanguage,
					systemLanguage,
					supportedLanguages
				),
				toolbar: getTinyMCEToolbar(),
				validButtons: getProperty(
					'glide.ui.html.editor.v4.toolbar.valid.buttons'
				),
				validPlugins: getProperty('glide.ui.html.editor.v4.valid_plugins'),
				plugins: getProperty('glide.ui.html.editor.v4.enabled_plugins'),
				fonts: getProperty('glide.ui.html.editor.v4.font.collection'),
				removeHost: isAttrTrue(getProperty('glide.ui.html.editor.remove_host')),
				relativeUrls: isAttrTrue(
					getProperty('glide.ui.html.editor.relative_urls')
				),
				convertUrls: isAttrTrue(
					getProperty('glide.ui.html.editor.convert_urls')
				),
				extendedValidElements: getProperty(
					'glide.ui.html.editor.extended_valid_elements'
				),
				configMentions: {
					enableMentions: controlProps.enableMentions,
					resourceTable: field.referringTable,
					resourceSysId: field.referringRecordId
				},
				...htmlAttributes
			};
			Control = 'now-record-html-editor';
			break;
		}

		case FIELD_TYPE.FILE_ATTACHMENT:
			Control = 'sn-record-file-attachment';
			controlProps = {
				maxAttachmentSize: getProperty('com.glide.attachment.max_size'),
				...controlProps,
				tableName: controlProps.referringTable,
				tableSysId: controlProps.referringRecordId,
				contentType: controlProps.contentType,
				virusState: controlProps.state,
				extensions: getProperty('glide.attachment.extensions')
			};
			break;

		case FIELD_TYPE.DURATION:
			Control = 'sn-record-duration';
			controlProps = {
				...controlProps,
				value: normalizeDurationValue(controlProps.value)
			};
			break;

		case FIELD_TYPE.ANNOTATION:
			Control = 'sn-record-annotation';
			controlChildren = field.isPlainText ? field.text : [toHtml(field.text)];
			switch (field.typeDisplayValue) {
				case 'Info Box Red':
					controlProps.important = true;
					break;
				case 'Line Separator':
					controlProps.lineSeparator = true;
					break;
				case 'Section Separator':
					controlProps.sectionSeparator = true;
					break;
			}
			break;

		case FIELD_TYPE.IP_ADDR:
		case FIELD_TYPE.IP_ADDRESS:
			Control = 'now-record-ip-address';
			break;

		case FIELD_TYPE.TEMPLATE_VALUE:
			Control = 'sn-record-template-value-connected';
			break;

		default:
			Control = 'sn-record-input';
			controlProps = getControlPropsForUnSupported(controlProps, type);
	}

	return {
		Control,
		props: controlProps,
		children: controlChildren,
		render: true
	};
}

export default {
	isSupported,
	createFormControl,
	getFormControl
};
