import { some, matches, noop, isEqual } from 'lodash';

import {
	queryBaseActionHandlers,
	queryBaseFetchDataEffectHandler
} from '@devsnc/sn-glide-query-base';
import { actionTypes } from '@servicenow/ui-core';

import { DEPENDENCY_PROPS, normalizeDisplayValue } from './constants';

const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

const query = `
query ($table: String!, $field: String!, $sys_id: String, $chars: String!, $paginationLimit: Int, $paginationOffset: Int, $dependentValue: String) {
  GlideLayout_Query {
    referenceDataRetriever(
      tableName: $table,
      fieldName: $field,
      sysId: $sys_id,
      chars: $chars,
      pagination: { limit: $paginationLimit, offset: $paginationOffset },
      ignoreTotalCount: true,
      sysparm_ignore_ref_qual: true,
      sysparm_ref_override: $dependentValue,
      ignoreDepAsRefQual: true
    ) {
      totalCount
      recentCount
      matchesCount
      referenceRecentDataList {
        sysId
        referenceData {
          key
          value
        }
      }
      referenceDataList {
        sysId
        referenceData {
          key
          value
        }
      }
    }
  }
}
`;

const variableList = [
	'sys_id',
	'table',
	'field',
	'chars',
	'paginationLimit',
	'dependentValue'
];

const isDependencyChanged = name => some(DEPENDENCY_PROPS, matches(name));

/**
 * Handles COMPONENT_PROPERTY_CHANGED
 */
const handlePropertyChange = ({ action, properties, updateState }) => {
	const {
		payload: { name, value, previousValue }
	} = action;
	const { name: fieldName, displayValue, onValueChange = noop } = properties;

	// short-circuit if the values are the same
	if (isEqual(value, previousValue)) return;

	// Dependency changes
	if (isDependencyChanged(name)) {
		updateState({
			normalizedDisplayValue: ''
		});
		onValueChange(null, fieldName, '', '');
	}
	// Live updates
	else if (name === 'liveUpdate') {
		updateState({
			normalizedDisplayValue: normalizeDisplayValue(displayValue)
		});
	}
};

/**
 * Handles COMPONENT_BOOTSTRAPPED
 */
const handleComponentBootstrapped = ({
	updateState,
	properties: { displayValue }
}) => {
	updateState({
		normalizedDisplayValue: normalizeDisplayValue(displayValue)
	});
};

const queryBaseActionHandlersData = queryBaseActionHandlers(
	query,
	variableList
);
export const queryBaseFetchDataEffect = queryBaseFetchDataEffectHandler(
	query,
	variableList
);

export default {
	...queryBaseActionHandlersData,
	[COMPONENT_BOOTSTRAPPED]: handleComponentBootstrapped,
	[COMPONENT_PROPERTY_CHANGED]: handlePropertyChange
};
