// 3rd party imports
import React from 'react';
import PropTypes from 'prop-types';
import { noop, omit, size, get, isEqual, map } from 'lodash';

import { fieldPropTypes } from '@devsnc/sn-controls-common';
import { parse, format, formatServer } from './currencyFormat';

import { SNInput } from '@devsnc/sn-record-input';

import { withState } from '@devsnc/sn-record-input-connected';
import { ConnectedInput } from '@devsnc/sn-record-input-connected';

import shallowEqual from '../common/utils/shallowEqual';

import { isAttrTrue } from '@devsnc/sn-controls-common';

import '@devsnc/sn-record-dropdown-internal';

/**
 * Allows users to enter and edit currency values and country codes
 */
export class Currency extends React.Component {
	onSelectChange = ({ value: currencyCode }) => {
		this.props.onValueChange(
			{
				value: this.props.value,
				additionalInfo: { currencyCode }
			},
			true
		);
	};

	shouldComponentUpdate(nextProps) {
		const { currencyCodes: nextCurrencyCodes, ...restOfNextProps } = nextProps;
		const { currencyCodes: oldCurrencyCodes, ...restOfOldProps } = this.props;

		return !(
			shallowEqual(restOfNextProps, restOfOldProps) &&
			isEqual(nextCurrencyCodes, oldCurrencyCodes)
		);
	}

	isReadOnly = () => (isAttrTrue(this.props.readonly) ? true : undefined);

	isDropdownDisabled = () => this.props.singleCurrency;

	render() {
		const {
			name,
			invalid,
			messages,
			value,
			currencyCode,
			currencyCodes
		} = this.props;

		let attrs = omit(
			this.props,
			'initialValue',
			'displayValue',
			'defaultCurrency',
			'currencyCodes',
			'useAllFractionsDigits',
			'maxFractionDigits',
			'locale',
			'currencyCode',
			'singleCurrency',
			'dropdownOpen'
		);

		return (
			<ConnectedInput
				{...attrs}
				invalid={invalid}
				messages={messages}
				maxlength="19"
				name={name + '_input'}
				value={value}
				align="end"
				// Temporary fix for london
				ignoreDropdown={true}
			>
				{size(currencyCodes) > 0 && (
					<sn-record-dropdown-internal
						slot="controls"
						on-change={this.onSelectChange}
						manage-selected-items
						selected-items={[currencyCode]}
						items={map(currencyCodes, ({ value, displayValue }) => ({
							id: value,
							label: displayValue
						}))}
						bare
						{...(this.isDropdownDisabled() ? { disabled: true } : {})}
						{...(this.isReadOnly() ? { readonly: true } : {})}
						hide-caret={size(currencyCodes) === 1}
					/>
				)}
			</ConnectedInput>
		);
	}
}

const ConnectedCurrency = withState(Currency, {
	parse: (value, props, { currencyCode }) => {
		let { value: parsedValue, error } = parse(
			value,
			props.locale,
			currencyCode
		);
		return {
			value: error ? parsedValue : formatServer(parsedValue, currencyCode),
			error
		};
	},
	format: (value, props, state = {}) => {
		const { currencyCodes } = props;
		const { currencyCode } = state;

		const defaultCurrency =
			currencyCode ||
			props.defaultCurrency ||
			get(currencyCodes, '0.value') ||
			'USD';

		const formattedCurrency = format(
			value,
			defaultCurrency,
			props.locale,
			props.useAllFractionsDigits,
			props.maxFractionDigits
		);

		if (formattedCurrency.error) {
			return {
				...formattedCurrency,
				currencyCode
			};
		}

		return formattedCurrency;
	},
	stopBubbling: ['SN_RECORD_INPUT_CONNECTED']
});
export default ConnectedCurrency;

ConnectedCurrency.style = SNInput.style;
ConnectedCurrency.propTypes = {
	...fieldPropTypes,

	currencyCode: PropTypes.string,

	dropdownOpen: PropTypes.bool,

	/**
	 * Initial value for the input.
	 */
	initialValue: PropTypes.string,

	/**
	 * When the `value` string does not contain a currency code, use this default
	 * currency code instead. Will be ignored if `value` is prefixed with a
	 * currency code.
	 */
	defaultCurrency: PropTypes.string,

	/**
	 * System locale setting used to format the currency
	 */
	locale: PropTypes.string,

	/**
	 * Should be locked into using a single currency
	 */
	singleCurrency: PropTypes.bool,

	/**
	 * System setting to allow all digits entered regardless of currency format
	 */
	useAllFractionsDigits: PropTypes.oneOfType([
		PropTypes.bool,
		PropTypes.string
	]),

	/**
	 * Maximum number of fraction digits to show
	 */
	maxFractionDigits: PropTypes.number,

	/**
	 * List of available currencies that the user can enter.
	 */
	currencyCodes: PropTypes.arrayOf(
		PropTypes.shape({
			/**
			 * Value for the currency code
			 */
			value: PropTypes.string,

			/**
			 * User-friendly symbol of the code.
			 *
			 * @deprecated the currency symbol is now inferred from the currency code
			 * automatically.
			 */
			displayValue: PropTypes.string
		})
	)
};

ConnectedCurrency.defaultProps = {
	defaultCurrency: 'USD',
	value: '',
	locale: window.navigator.language,
	dispatch: noop,
	currencyCodes: [],
	maxFractionDigits: 4
};
