import { t } from 'sn-translate';
import { parseArabic } from '@devsnc/sn-controls-common';

const serverFormatRegex = /^([a-zA-Z]{3});(-?\d*(\.\d+)?)$/g;
const floatFormatRegex = /^(-?\d*(\.\d+)?)$/g;

export const parseSeparator = replacement => regex => value =>
	typeof value === 'string' ? value.replace(regex, replacement) : value;

export const format = (
	value,
	defaultCurrency,
	locale,
	useAllFractionsDigits,
	maxFractionDigits
) => {
	const serverFormat = new RegExp(serverFormatRegex);
	const floatFormat = new RegExp(floatFormatRegex);
	let currencyCode, error;

	// We test the value to see if it matches the server format and if so parse out the
	// currency code from it
	const serverFormatMatch = serverFormat.exec(value);
	if (serverFormatMatch) {
		[, currencyCode, value] = serverFormatMatch;
	} else {
		currencyCode = defaultCurrency;
	}

	// if the value is not set skip formatting
	if (value === null || value === undefined || value === '') {
		value = '';
	} else if (floatFormat.test(value)) {
		// else check if value is a float	and format it if so
		// when useAllFractionsDigits is pased in we want to force Intl.NumberFormat to persist all
		// fraction digits, otherwise don't set these properties and rely on the locale+currency to
		// drive.
		const formatOptions = {
			minimumFractionDigits: useAllFractionsDigits ? 0 : undefined,
			maximumFractionDigits: useAllFractionsDigits
				? maxFractionDigits
				: undefined
		};
		value = new Intl.NumberFormat(locale, formatOptions).format(value);
	} else {
		// else pass through value and add an error
		error = { type: 'error', message: t('{0} is not a valid amount', value) };
	}
	return {
		value,
		currencyCode,
		error
	};
};

export const parse = (value, locale) => {
	let error;

	// if the value is not set skip parsing
	if (value === null || value === undefined || value === '') {
		value = '';
	} else {
		// else attempt to parse the value passed in
		const floatFormat = new RegExp(floatFormatRegex);
		const numWithThousands = new Intl.NumberFormat(locale).format('1111');
		const numWithFractions = new Intl.NumberFormat(locale).format('1.11');

		const thousandSeparator =
			numWithThousands.length === 4 ? '' : numWithThousands.slice(1, 2);
		const decimalSeparator = numWithFractions.slice(1, 2);
		const strip = parseSeparator('');
		const sanitizers = [
			parseArabic,
			strip(new RegExp(`\\${thousandSeparator}`, 'g')),
			parseSeparator('.')(new RegExp(`\\${decimalSeparator}`, 'g')),
			strip(/[\s\u00A0]/g)
		];

		const processedValue = sanitizers.reduce(
			(sanatizedValue, parser) => parser(sanatizedValue),
			value
		);

		if (floatFormat.test(processedValue)) {
			value = processedValue;
		} else {
			error = { type: 'error', message: t('{0} is not a valid amount', value) };
		}
	}
	return {
		value,
		error
	};
};

export const formatServer = (value, currencyCode) =>
	value ? `${currencyCode};${value}` : '';
