export const query = `query ($parentTableName: String!, $fieldName: String!, $recordSysId: String!, $serializedChanges: String!, $encodedRecord: String!, $tableName: String!, $path: String!) {
	GlideLayout_Query {
	  fieldNameDataRetriever(parentTableName: $parentTableName, fieldName: $fieldName, recordSysId: $recordSysId, serializedChanges: $serializedChanges, encodedRecord: $encodedRecord, tableName: $tableName, path: $path) {
		fieldNamesList {
		  tableName
		  fieldNameData {
			value
			displayValue
			isFieldReferenceType
			referenceDataKey
		  }
		}
	  }
	}
  }
  `;
