import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { get } from 'lodash';
import {
	constructDataModel,
	transformFieldNameMetadata
} from './transformFieldName';
import { query } from './query';
import {
	getEncodedRecord,
	isAttrTrue,
	getSerializedChanges,
	adaptCallbackForLegacyAPI
} from '@devsnc/sn-controls-common';
import { t } from 'sn-translate';
import _ from 'lodash';

import {
	FETCH_DATA,
	FETCH_DATA_SUCCESS,
	FETCH_DATA_FAILURE,
	FETCH_LEAF_DATA,
	FETCH_LEAF_DATA_SUCCESS,
	FETCH_LEAF_DATA_FAILURE,
	DEPENDENT_FIELD_VALUE,
	LIVE_UPDATE,
	FIELDS_NAMES_RESULTS_PATH
} from './constants';

export const nowPopOverOpenedEffect = ({
	state,
	properties,
	dispatch,
	action,
	updateState
}) => {
	const { value: opened } = action.payload;
	const { readonly, isReadOnly: dependentValueEmpty } = state.properties;
	const isReadOnly = isAttrTrue(readonly) ? readonly : dependentValueEmpty;

	if (!isAttrTrue(isReadOnly)) {
		if (opened) {
			let {
				name,
				referringRecordId,
				formData = {},
				referringTable: parentTableName,
				dependentFieldValue: tableName,
				value: path = ''
			} = state.properties;

			const payload = {
				parentTableName,
				fieldName: name,
				recordSysId: referringRecordId,
				serializedChanges: getSerializedChanges(properties),
				encodedRecord: getEncodedRecord(formData),
				tableName,
				path
			};

			dispatch(FETCH_DATA, payload);
		} else {
			updateState({
				opened: opened
			});
		}
	}else{
		updateState({
			opened: false
		});
	}
};

export const dotWalkLeafHandler = ({
	action: {
		payload: { referenceSource, fieldName, displayValue, source, pathIndex }
	},
	dispatch,
	updateState,
	state: {
		properties: {
			name,
			referringRecordId,
			formData = {},
			referringTable: parentTableName
		},
		dataModel
	},
	properties
}) => {
	const { path } = dataModel;

	let updatedPath;

	if (pathIndex > path.length - 1) {
		updatedPath = [...path, { displayValue, field: fieldName, parent: source }];
	} else {
		updatedPath = path.slice(0, pathIndex + 1);
		const lastUpdatedPathItem = updatedPath[updatedPath.length - 1];
		lastUpdatedPathItem.field = fieldName;
		lastUpdatedPathItem.displayValue = displayValue;
	}
	updateState({
		dataModel: {
			...dataModel,
			path: updatedPath,
			leafPathSource: referenceSource
		}
	});

	const payload = {
		parentTableName,
		fieldName: name,
		recordSysId: referringRecordId,
		serializedChanges: getSerializedChanges(properties),
		encodedRecord: getEncodedRecord(formData),
		tableName: referenceSource,
		path: ''
	};
	dispatch(FETCH_LEAF_DATA, payload);
};

export const fetchEffect = createGraphQLEffect(query, {
	variableList: [
		'parentTableName',
		'fieldName',
		'recordSysId',
		'serializedChanges',
		'encodedRecord',
		'tableName',
		'path'
	],
	successActionType: FETCH_DATA_SUCCESS,
	errorActionType: FETCH_DATA_FAILURE
});

export const fetchSuccessEffect = ({
	state: {
		dataModel,
		properties: {
			value: pathValue,
			leafPathSource,
			dictionaryAttributes: { allowDotWalking = false, referenceTypes },
			dependentFieldValue: tableName
		},
		liveUpdate
	},
	updateState,
	updateProperties,
	action: { payload }
}) => {
	//liveUpdate from state
	if (liveUpdate) {
		let data = get(payload, FIELDS_NAMES_RESULTS_PATH);
		const pathArray = pathValue.split('.');
		const valueObj = data[data.length - 1]['fieldNameData'].filter(
			e => e.value === pathArray[pathArray.length - 1]
		);
		updateProperties({
			displayValue: valueObj[0].displayValue,
			liveUpdate: false
		});

		updateState({
			opened: false,
			liveUpdate: false
		});
	}
	const results = get(payload, FIELDS_NAMES_RESULTS_PATH);

	const dataModelConstructed = constructDataModel(
		results,
		tableName,
		pathValue,
		leafPathSource,
		referenceTypes,
		allowDotWalking
	);

	let dataModelToUpdate = {
		dataModel: dataModelConstructed,
		opened: !!(dataModelConstructed.metadata && dataModelConstructed.path),
		error: null
	};
	if (dataModel) {
		dataModelToUpdate = {
			dataModel: {
				...dataModel,
				metadata: {
					...dataModel.metadata,
					...dataModelConstructed.metadata
				},
				path: dataModelConstructed.path,
				leafPathSource: dataModelConstructed.leafPathSource
			},
			opened: !!(dataModelConstructed.metadata && dataModelConstructed.path),
			error: null
		};
	}
	//donot open popover even after getting data
	if (liveUpdate && pathValue) {
		dataModelToUpdate.opened = false;
	}

	updateState(dataModelToUpdate);
};

export const fetchFailureEffect = ({
	state: {
		properties: { value, displayValue, onValueChange, name }
	},
	updateState
}) => {
	let errorMessage = t('Unable to fetch Field . Please try again');
	//reset liveupdate state on failure
	updateState({
		liveUpdate: false
	});
	if (onValueChange) {
		adaptCallbackForLegacyAPI(onValueChange)({
			name,
			value,
			displayValue,
			error: {
				type: 'error',
				message: errorMessage
			}
		});
	}
};

export const fetchLeafEffect = createGraphQLEffect(query, {
	variableList: [
		'parentTableName',
		'fieldName',
		'recordSysId',
		'serializedChanges',
		'encodedRecord',
		'tableName',
		'path'
	],
	successActionType: FETCH_LEAF_DATA_SUCCESS,
	errorActionType: FETCH_LEAF_DATA_FAILURE
});

export const fetchLeafSuccessEffect = ({
	state: {
		dataModel,
		properties: {
			leafPathSource,
			dictionaryAttributes: { referenceTypes, allowDotWalking = false }
		}
	},
	updateState,
	action: { payload }
}) => {
	const results = get(payload, FIELDS_NAMES_RESULTS_PATH);

	const metadataConstructed = transformFieldNameMetadata(
		results,
		referenceTypes,
		allowDotWalking
	);

	let dataModelToUpdate = {
		dataModel: {
			metadata: metadataConstructed,
			path: [],
			leafPathSource
		},
		error: null
	};
	if (dataModel) {
		dataModelToUpdate = {
			dataModel: {
				...dataModel,
				metadata: {
					...dataModel.metadata,
					...metadataConstructed
				}
			},
			error: null
		};
	}
	updateState(dataModelToUpdate);
};

export const fetchLeafFailureEffect = ({
	state: {
		properties: { value, displayValue, onValueChange, name }
	}
}) => {
	let errorMessage = t('Unable to fetch reference Fields. Please try again');
	if (onValueChange) {
		adaptCallbackForLegacyAPI(onValueChange)({
			name,
			value,
			displayValue,
			error: {
				type: 'error',
				message: errorMessage
			}
		});
	}
};

export const pathUpdatedHandler = ({
	state: {
		dataModel,
		properties: { onValueChange, name }
	},
	action: {
		payload: { path }
	},
	updateState
}) => {
	const updatedPath = path || [];
	let leafPathSource = '';
	if (updatedPath.length === 0) {
		leafPathSource = dataModel.path[0].parent;
	}

	updateState({
		opened: false,
		dataModel: {
			...dataModel,
			path: updatedPath,
			leafPathSource
		},
		error: null
	});

	const value = path.map(x => x.field).join('.');
	const displayValue =
		path.length > 0 ? path[path.length - 1].displayValue : '';

	if (onValueChange) {
		adaptCallbackForLegacyAPI(onValueChange)({
			name,
			value,
			displayValue
		});
	}
};

export const updateBreadCrumbHandler = ({
	state: { dataModel },
	action: {
		payload: { path }
	},
	updateState
}) => {
	const updatedPath = path || [];
	let leafPathSource = null;
	if (updatedPath.length === 0) {
		leafPathSource = dataModel.path[0].parent;
	}

	updateState({
		dataModel: {
			...dataModel,
			path: updatedPath,
			leafPathSource
		},
		error: null
	});
};

export const propertyChangeHandler = ({
	state,
	action,
	updateState,
	updateProperties
}) => {
	const {
		payload: { name: updatedProperty, value }
	} = action;

	if (
		updatedProperty &&
		LIVE_UPDATE === updatedProperty &&
		value === true &&
		state.properties.value
	) {
		//use state liveUpdate as the prop updates comes in random order
		updateState({
			liveUpdate: true
		});
	} else if (updatedProperty && DEPENDENT_FIELD_VALUE === updatedProperty) {
		const {
			name,
			onValueChange,
			dependentFieldValue,
			dependentFieldLabel,
			readonly,
			isReadOnly
		} = state.properties;

		updateState({
			opened: false
		});

		if (
			onValueChange &&
			_.isEmpty(dependentFieldValue) &&
			!isAttrTrue(readonly)
		) {
			let infoMessage = t(
				`Select a ${dependentFieldLabel} before modifying this field`
			);
			if (!isAttrTrue(isReadOnly)) {
				updateProperties({ isReadOnly: true });
			}

			adaptCallbackForLegacyAPI(onValueChange)({
				name,
				value: '',
				displayValue: '',
				error: {
					type: 'info',
					message: infoMessage
				}
			});
		} else if (onValueChange && !isAttrTrue(readonly)) {
			if (isAttrTrue(isReadOnly)) {
				updateProperties({ isReadOnly: false });
			}
			adaptCallbackForLegacyAPI(onValueChange)({
				name,
				value: '',
				displayValue: ''
			});
		}
	}
};
