export const requestForTypesAndMetaData = `
query ($queryString: String!, $encodedRecord: String, $isPartial: Boolean!, $serializedChanges: String!, $fieldName: String!, $recordSysId: String!, $referringTable: String!) {
  GlideLayout_Query {
    templateValueFieldsQuery(queryString: $queryString, encodedRecord: $encodedRecord, isPartial: $isPartial, serializedChanges: $serializedChanges, fieldName:$fieldName, recordSysId: $recordSysId, recordTableName: $referringTable) {
      tableName
      valuesList {
        displayValue
        value
        fieldName
				fieldLabel
				shouldHide
      }
      fields {
        name: elementName
        dictionary: dictionaryData {
          type: dictionaryType
          dependentField
          dependentTable
          name
          label
          canWrite
          canRead
          canCreate
          internalType
          isMandatory
          sys_readonly: sysReadonly
          attributes {
            name
            value
          }
        }
        ... on GlideLayout_TableNameElementType {
          displayValue
        }
        ... on GlideLayout_FileAttachmentElementType {
          value
          displayValue
          state
          contentType
        }
        ... on GlideLayout_ChoiceElementType {
          choices {
            displayValue
            value
          }
        }
        ... on GlideLayout_CurrencyElementType {
          value
          code
          defaultCurrency
          currencyCodes {
            code
            symbol
          }
        }
        ... on GlideLayout_PhoneNumberElementType {
          value
          country
          countryCodes {
            code
            name
          }
        }
        ... on GlideLayout_GlideListElementType {
          reference
        }
        ... on GlideLayout_ReferenceElementType {
          reference
          referenceQualifier
          referenceKey
          useReferenceQualifier
          dependent
          dependentOnField
          refAutoCompleter
          refAcOrderBy
          refAcColumns
          refAcColumnsSearch
          refAcDisplayValue
          refQualElements
          refContributions
          isDynamicCreate
          isReferenceScriptableTable
          referenceError
        }  
      }
    }
  }
}
`;
