import { createCustomElement } from '@servicenow/ui-core';
import { get } from 'lodash';
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-loader';
import '@devsnc/sn-record-control-wrapper';

import { getFormControl } from '../base/controlElementFactory';

import { GLIDE_LIST, REFERENCE } from '../base/FieldType';

import styles from './field-selection.scss';

import '@devsnc/sn-form-controls';
import '@servicenow/now-button';
import * as FIELD_TYPE from '../base/FieldType';

import { t } from 'sn-translate';

import { flow, omit } from 'lodash';
import { isAttrTrue } from '@devsnc/sn-controls-common';
export const FIELD_SELECTION = 'sn-record-template-value-field-selection';

export const FIELD_SELECTION_ACTION_NAME = FIELD_SELECTION.toUpperCase().replace(
	/\-/g,
	'_'
);
export const FIELD_SELECTION_PATH_UPDATE = `${FIELD_SELECTION_ACTION_NAME}#PATH_UPDATED`;
export const FIELD_VALUE_CHANGED = `${FIELD_SELECTION_ACTION_NAME}#VALUE_CHANGED`;

const searchDeclarativeAction = {
	name: 'reference_search',
	icon: 'magnifying-glass-outline',
	label: t('Search For Record'),
	dependency: '',
	requiresValue: false,
	order: 0,
	conditions: '',
	actionType: 'action_component',
	actionComponent: 'sn-declarative-reference-search',
	actionDispatch: '',
	actionPayload: '',
	actionAttributes: '{"context":""}',
	groupBy: false,
	group: '',
	assignmentId: '994fc9bd53332300099addeeff7b1296',
	confirmationRequired: false,
	confirmationMessage: '',
	tooltip: t('Search for Record'),
	modelConditions: [],
	payloadMap: []
};

const createHandler = (matchingType, transformer) => type => props =>
	type === matchingType ? { ...props, ...transformer(props) } : props;

const handleGlideList = createHandler(GLIDE_LIST, props => ({
	// referringTable should be the value that the TableName field has
	referringTable: props.table,
	display_value_list: props.displayValue
}));

const handleReference = createHandler(REFERENCE, props => ({
	// referringTable should be the value that the TableName field has
	referringTable: props.table,
	declarativeUiActions: [searchDeclarativeAction]
}));

const mapProps = type => flow(handleGlideList(type), handleReference(type));

const empty = { Control: Fragment, props: {} };

const DeleteButton = ({ identifier, fieldLabel, displayValue, style }) => (
	<now-button
		style={style}
		icon="close-outline"
		size="md"
		variant="tertiary"
		disabled={identifier === 'newFieldCreator'}
		append-to-payload={{ intent: 'remove', identifier }}
		configAria={{
			'aria-label': t(
				`Remove template row ${identifier +
					1} with condition ${fieldLabel} equals ${displayValue}`
			)
		}}
		tooltipContent={t('Delete Row')}
	/>
);

const view = (state, { dispatch }) => {
	const {
		fieldName,
		fieldProps,
		fieldLabel,
		displayValue,
		messages,
		table,
		type,
		key,
		path,
		padBottom,
		identifier,
		choices,
		isLoading,
		readonly,
		isUnsupportedField,
		isNewField,
		formData
	} = state.properties;

	const value = get(state, 'properties.value');

	const isReadOnly = isAttrTrue(readonly);

	if (!table) {
		return null;
	}

	if (
		(isReadOnly &&
			type !== FIELD_TYPE.HTML &&
			type !== FIELD_TYPE.TRANSLATED_HTML) ||
		isUnsupportedField
	) {
		return (
			<Fragment>
				<div class={{ 'field-selection-container-readonly': true }}>
					<div class={{ 'field-selection-label-readonly': true }}>
						<div>
							<div>{fieldLabel}</div>
							{isUnsupportedField ? (
								<sn-record-controls-internal-field-messages
									messages={[
										{
											type: 'info',
											message: t('This field is not supported.', type)
										}
									]}
									noHorizontalPadding
								/>
							) : null}
						</div>
					</div>
					<div class={{ 'field-selection-value-readonly': true }}>
						{type === FIELD_TYPE.PERCENT_COMPLETE ? (
							displayValue + '%'
						) : type === FIELD_TYPE.URL ? (
							<now-text-link
								label={displayValue}
								href={displayValue}
								variant="secondary"
								opensWindow={true}
							/>
						) : (
							displayValue
						)}
					</div>
					{isUnsupportedField ? (
						<DeleteButton
							identifier={identifier}
							fieldLabel={fieldLabel}
							displayValue={displayValue}
						/>
					) : null}
				</div>
			</Fragment>
		);
	}

	const { Control, props } = fieldName
		? getFormControl(
				state.componentId,
				type,
				mapProps(type)({
					...fieldProps,
					referringRecordId: '-1',
					value,
					displayValue,
					name: fieldName,
					messages,
					table,
					readonly
				}),
				({ value, displayValue, error }) => {
					dispatch(FIELD_VALUE_CHANGED, {
						value,
						displayValue,
						identifier,
						error
					});
				},
				null,
				formData
		  )
		: empty;

	props['value'] = value;

	return (
		<div
			key={key}
			class={{ 'field-selection-container': true, 'pad-bottom': padBottom }}
		>
			<div class={{ 'field-selection-fields-container': true }}>
				<div class={{ 'field-selection-selector': true }}>
					{!isReadOnly ? (
						<sn-record-choice-search
							name={fieldName}
							class={{ 'block-style': true }}
							displayValue={fieldLabel}
							choices={choices}
						>
							<div slot="triggerButton">
								<sn-record-choice
									tabindex="-1"
									displayValue={fieldLabel}
									value={isNewField ? undefined : '0'}
									slot="trigger"
									placeholder={t('-- choose field --')}
								/>
							</div>
						</sn-record-choice-search>
					) : (
						<span>{fieldLabel}</span>
					)}
				</div>
				<div
					class={{ 'field-selection-field': true }}
					aria-label={t(`Set Value for ${fieldLabel}`)}
				>
					{isLoading ? (
						<now-loader class={{ 'loading-line': true }} size="lg" />
					) : props.name ? (
						<Control {...omit(props, ['label'])} />
					) : (
						<sn-record-input readonly placeholder={t('-- value --')} />
					)}
				</div>
			</div>

			{!isReadOnly ? (
				<div class={{ 'field-selection-remove-row': true }}>
					<DeleteButton
						identifier={identifier}
						fieldLabel={fieldLabel}
						displayValue={displayValue}
						style={{ visibility: isLoading ? 'hidden' : 'visible' }}
					/>
				</div>
			) : null}
		</div>
	);
};

/**
 * This component is for internal us buy the controls team.
 */
createCustomElement(FIELD_SELECTION, {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	actionHandlers: {
		['SN_CHOICE_SEARCH_DROPDOWN#ITEM_SELECTED']: {
			effect: ({ state, action, dispatch }) => {
				const { id, displayValue } = action.payload;

				dispatch(FIELD_SELECTION_PATH_UPDATE, {
					fieldLabel: displayValue,
					identifier: state.properties.identifier,
					path: id
				});
			},
			stopPropagation: true
		}
	},
	properties: {
		fieldName: { default: '' },
		fieldLabel: { default: '' },
		fieldProps: {},
		isLoading: {},
		displayValue: {},
		value: {},
		type: {},
		messages: {},
		table: {},
		readonly: { default: false },
		isUnsupportedField: { default: false },
		key: {},
		choices: {},
		path: { default: '' },
		isNewField: { default: false },
		padBottom: { default: false },
		identifier: {},
		formData: {}
	},
	dispatches: {}
});
