import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { uniqBy, compact, find, pick, get, omit, isEqual } from 'lodash';
import { getSerializedChanges, effectProxy } from '@devsnc/sn-controls-common';
import { isFunction } from 'lodash';
import * as FIELD_TYPE from '../base/FieldType';
import { adaptCallbackForLegacyAPI } from '@devsnc/sn-controls-common';

import {
	FIELD_SELECTION_PATH_UPDATE,
	FIELD_VALUE_CHANGED
} from './field-selection';

import { actionTypes } from '@servicenow/ui-core';
const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED } = actionTypes;
import { requestForTypesAndMetaData } from './graphql';
import { t } from 'sn-translate';

export const COMPONENT_PREFIX = 'SN_RECORD_TEMPLATE_VALUE';

const DATA_REQUEST = `${COMPONENT_PREFIX}#DATA_REQUEST`;
export const DATA_REQUEST_SUCCESS = `${COMPONENT_PREFIX}#DATA_REQUEST_SUCCESS`;
const DATA_REQUEST_ERROR = `${COMPONENT_PREFIX}#DATA_REQUEST_ERROR`;
const SET_VALUE = `${COMPONENT_PREFIX}#SET_VALUE`;
const pathToValue = 'payload.data.GlideLayout_Query.templateValueFieldsQuery';
const addedPayloadPath = 'meta.options.templateVars.addedPayload';
const isPartialPath = 'meta.options.variables.isPartial';
const WORKSPACE_TYPES = Object.keys(FIELD_TYPE).map(key => FIELD_TYPE[key]);

const getFieldsHaveErrorMessage = () => {
	const message = t('Form template has an error in one of its fields');
	return {
		type: 'error',
		message
	};
};
const getFieldWithError = fieldValues =>
	find(fieldValues, f => find(f.messages, ['type', 'error']));

const calculatedFieldError = newFieldValues =>
	getFieldWithError(newFieldValues) ? getFieldsHaveErrorMessage() : undefined;

export const serializeValue = value => {
	if (value === undefined) return '';
	if (typeof value === 'string') return value.replace(/\^/g, '^^');
	return value;
};

export const constructValue = (fieldValues = []) =>
	fieldValues
		.map(field => ({ ...field, value: serializeValue(field.value) }))
		.map(field => (field.path || field.fieldName) + '=' + field.value)
		.join('^')
		.concat('^EQ');

const createField = (field, fieldData, properties) => ({
	...field,
	value: field.value !== '' ? field.value : undefined,
	displayValue: fieldData.displayValue
		? fieldData.displayValue
		: field.displayValue,
	fieldName: fieldData.name,
	fieldLabel: field.fieldLabel,
	fieldProps: {
		...omit(fieldData, ['dictionary']),
		...omit(fieldData.dictionary, ['attributes']),
		...pick(properties, ['referringRecordId', 'referringTable'])
	},
	dictionary: {
		reference: fieldData.reference
	},
	type: fieldData.dictionary.type,
	path: field.fieldName
});

const callOnValueChange = ({ name, value, onValueChange, error }) => {
	if (isFunction(onValueChange)) {
		adaptCallbackForLegacyAPI(onValueChange)({ name, value, error });
	}
};

const convertChoiceToFieldValue = (choice, properties) =>
	createField(
		{
			...choice,
			fieldName: choice.dictionary.name,
			fieldLabel: choice.dictionary.label,
			path: choice.id
		},
		choice,
		properties
	);

const editExistingField = (payload, fieldValues, allChoices, properties) =>
	fieldValues.map(element => {
		const choosenField = find(allChoices, ({ id }) => payload.path === id);
		if (element.path === payload.identifier) {
			return convertChoiceToFieldValue(choosenField, properties);
		} else {
			return element;
		}
	});

const updateConsumerAndState = ({
	name,
	newFieldValues,
	onValueChange,
	choices,
	updateState,
	dispatch,
	error
}) => {
	const updatedValue = constructValue(newFieldValues);

	callOnValueChange({ name, value: updatedValue, onValueChange, error });
	dispatch(SET_VALUE, { name, value: updatedValue, error });
	updateState({ fieldValues: newFieldValues, value: updatedValue });
	const newState = { fieldValues: newFieldValues, value: updatedValue };
	if (choices) newState.choices = choices;
	updateState(newState);
};

const dataWithNewField = ({
	oldFieldValues,
	newValue,
	newFieldData,
	properties
}) => [...oldFieldValues, createField(newValue, newFieldData, properties)];

const dataWithEditedField = ({
	oldFieldValues,
	identifier,
	newValue,
	newFieldData,
	properties
}) =>
	oldFieldValues.map(field => {
		if (field.path === identifier) {
			return createField(newValue, newFieldData, properties);
		}
		return field;
	});

const allNewData = ({ valuesList, fields, properties }) =>
	valuesList.map((field, index) =>
		createField(field, fields[index], properties)
	);

const cleanUpChoices = ({ fieldValues, allChoices = [] }) => {
	const alreadySelected = fieldValues.map(field => field.path);
	return uniqBy(
		allChoices
			.filter(({ shouldHide }) => !shouldHide)
			.filter(({ id }) => !alreadySelected.includes(id))
			.filter(({ type }) => WORKSPACE_TYPES.includes(type))
			.reverse(),
		field => {
			return field.id;
		}
	).reverse();
};

const filterOutTypeChangedValues = ({
	newFieldValues,
	oldFieldValues = []
}) => {
	const listOfPreviousTypes = oldFieldValues.reduce((prev, field) => {
		return { ...prev, [field.fieldName]: field.type };
	}, {});
	return newFieldValues.filter(field => {
		const oldType = listOfPreviousTypes[field.fieldName];
		return typeof oldType === 'undefined' || oldType === field.type;
	});
};

export const dataRequestGraphQL = createGraphQLEffect(
	requestForTypesAndMetaData,
	{
		templateVarList: ['addedPayload'],
		variableList: [
			'isPartial',
			'fieldName',
			'queryString',
			'recordSysId',
			'serializedChanges',
			'encodedRecord',
			'referringTable'
		],
		successActionType: DATA_REQUEST_SUCCESS,
		errorActionType: DATA_REQUEST_ERROR
	}
);

export default {
	[FIELD_SELECTION_PATH_UPDATE]: ({
		properties,
		state,
		updateState,
		action,
		dispatch
	}) => {
		const payload = action.payload;
		const { fieldValues, allChoices } = state;
		const { onValueChange, name } = properties;

		let newFieldValues;
		if (payload.identifier === 'newFieldCreator') {
			const choice = find(allChoices, function({ displayValue }) {
				return displayValue === payload.fieldLabel;
			});
			const fieldValue = convertChoiceToFieldValue(choice, properties);
			newFieldValues = [...fieldValues, fieldValue];
		} else {
			newFieldValues = editExistingField(
				payload,
				fieldValues,
				allChoices,
				properties
			);
		}

		updateConsumerAndState({
			name,
			newFieldValues,
			onValueChange,
			choices: cleanUpChoices({ fieldValues: newFieldValues, allChoices }),
			updateState,
			dispatch,
			error: calculatedFieldError(newFieldValues)
		});
	},
	[DATA_REQUEST]: effectProxy(dataRequestGraphQL),
	[DATA_REQUEST_SUCCESS]: ({
		action,
		updateState,
		state,
		properties,
		dispatch
	}) => {
		const isPartial = get(action, isPartialPath);
		const identifier = get(action, addedPayloadPath + '.identifiers[0]');
		const valuesList = get(action, pathToValue + '.valuesList');
		const fields = get(action, pathToValue + '.fields');
		const tableName = get(action, pathToValue + '.tableName');

		if (!tableName) {
			updateState({
				fieldValues: [],
				isLoading: false,
				isLoadingNewField: false,
				tempNewFieldLabel: undefined,
				table: ''
			});

			return;
		}

		let fieldValues;
		const mapArgs = {
			valuesList,
			oldFieldValues: state.fieldValues,
			identifier,
			newValue: valuesList[0],
			newFieldData: fields[0],
			properties,
			fields
		};
		const isNewField = identifier === 'newFieldCreator' && isPartial;
		// is full data reset
		const newFieldValues = allNewData(mapArgs);
		fieldValues = filterOutTypeChangedValues({
			newFieldValues,
			oldFieldValues: state.fieldValues
		});
		const newChoices = fields.map(field => ({
			...field,
			id: field.dictionary.name,
			displayValue: field.dictionary.label,
			type: field.dictionary.type,
			display: true
		}));
		const isValueRemovedBecauseOfTypeChange =
			newFieldValues.length > fieldValues.length;
		if (isValueRemovedBecauseOfTypeChange) {
			// We must let the parent component know we removed values
			updateConsumerAndState({
				name: properties.name,
				choices: cleanUpChoices({ fieldValues, allChoices: newChoices }),
				newFieldValues: fieldValues,
				onValueChange: properties.onValueChange,
				updateState,
				dispatch,
				error: calculatedFieldError(newFieldValues)
			});
		}
		updateState({
			isLoading: false,
			table: tableName,
			allChoices: newChoices,
			choices: cleanUpChoices({ fieldValues, allChoices: newChoices })
		});
		updateState({ fieldValues });
	},
	[DATA_REQUEST_ERROR]: ({ action }) => console.error(action.payload),
	[FIELD_VALUE_CHANGED]: ({
		dispatch,
		properties,
		state,
		updateState,
		action
	}) => {
		const { value, displayValue, identifier, error } = action.payload;
		const { name, onValueChange } = properties;

		const newFieldValues = state.fieldValues.map(field =>
			field.path === identifier
				? {
						...field,
						value,
						displayValue,
						messages: compact([error])
				  }
				: field
		);

		updateConsumerAndState({
			name,
			newFieldValues,
			onValueChange,
			updateState,
			dispatch,
			error: calculatedFieldError(newFieldValues)
		});
	},
	['NOW_BUTTON#CLICKED']: ({
		properties,
		state,
		updateState,
		action,
		dispatch
	}) => {
		const { onValueChange, name } = properties;
		const { identifier, intent } = action.payload;

		if (intent === 'remove') {
			const newFieldValues = state.fieldValues.filter(
				field => field.path !== identifier
			);

			updateConsumerAndState({
				name,
				choices: cleanUpChoices({
					fieldValues: newFieldValues,
					allChoices: state.allChoices
				}),
				newFieldValues,
				onValueChange,
				updateState,
				dispatch,
				error: calculatedFieldError(newFieldValues)
			});
		}
	},
	[COMPONENT_BOOTSTRAPPED]: ({ dispatch, properties, updateState }) => {
		let {
			name,
			value,
			formData: { encodedRecord },
			referringRecordId,
			referringTable
		} = properties;

		dispatch(DATA_REQUEST, {
			isPartial: false,
			queryString: value,
			fieldName: name,
			recordSysId: referringRecordId,
			serializedChanges: getSerializedChanges(properties),
			encodedRecord,
			referringTable
		});
		updateState({
			isLoading: true,
			value
		});
	},
	[COMPONENT_PROPERTY_CHANGED]: ({
		action,
		dispatch,
		properties,
		state,
		updateState,
		updateProperties
	}) => {
		const { name, value, previousValue } = action.payload;
		const {
			referringRecordId,
			value: queryString,
			name: fieldName,
			formData: { encodedRecord },
			referringTable
		} = properties;

		// short-circuit if the values are the same
		if (isEqual(value, previousValue)) return;

		const isTemplateTableChanged = serializedChangesString => {
			try {
				let serializedChanges = JSON.parse(serializedChangesString);

				let potentialChange = find(
					serializedChanges,
					({ name }) => name === properties.dependentField
				);

				if (!potentialChange) return false;

				// we want to fetch data only when the table changes
				return potentialChange.value !== state.table;
			} catch (e) {
				/* fall through and return false */
			}

			return false;
		};

		if (
			name === 'formData' &&
			isTemplateTableChanged(value.serializedChanges)
		) {
			dispatch(DATA_REQUEST, {
				isPartial: false,
				recordSysId: referringRecordId,
				queryString,
				fieldName,
				serializedChanges: getSerializedChanges(properties),
				encodedRecord,
				referringTable
			});
		} else if (name === 'value' && !isEqual(state.value, value)) {
			dispatch(DATA_REQUEST, {
				isPartial: false,
				recordSysId: referringRecordId,
				fieldName,
				serializedChanges: getSerializedChanges(properties),
				encodedRecord,
				queryString: value,
				referringTable
			});
			updateState({
				isLoading: true
			});
			updateProperties({
				liveUpdate: false
			});
		}
	}
};
