import React from 'react';
import PropTypes from 'prop-types';
import { isFunction, omit, size, get, isEqual, includes, map } from 'lodash';

import actionHandlers, {
	FETCH_PHONE_FORMATS,
	FETCH_PHONE_TERRITORY,
	NEEDS_PROCESSING_FETCH_STATUS,
	FETCH_PHONE_PROCESSED
} from './actionHandlers';
import * as PhoneFormatter from './phoneFormat';

import style from './phone.scss';

import { SNInput } from '@devsnc/sn-record-input'; // sn-choice-search is registred because of this (implicit and ugly)
import { ConnectedInput } from '@devsnc/sn-record-input-connected';
import { withState } from '@devsnc/sn-record-input-connected';

import shallowEqual from '../common/utils/shallowEqual';
import { isAttrTrue } from '@devsnc/sn-controls-common';

const AREA_CODE_MATCH_LIMIT = 40;

export const getCountryCode = (props, state) => {
	const { formData } = props;
	let userSetCode = formData.classicForm
		? get(props, 'behaviors.wsUserData.countryCallingCode', undefined)
		: get(formData, 'userSession.countryCallingCode', undefined);

	return (
		(state && state.countryCode) ||
		props.countryCode ||
		userSetCode ||
		props.defaultCode ||
		get(props.countryCodes, '0.value')
	);
};
const addPlusSign = countryCode => {
	if (includes(countryCode, '+')) return countryCode;

	return '+' + countryCode;
};
export const getFormattedPlaceholder = formats => {
	const format = get(formats, '[0].format.value', '');
	const pattern = get(formats, '[0].pattern.value', '');
	return PhoneFormatter.getPlaceholder(format, pattern);
};

/**
 * Specialized input to handle phone numbers. Can optionally render a button to
 * trigger a call to the given phone number.
 */
export class Phone extends React.Component {
	onSelectChange = oldCountryCode => ({ value: countryCode, displayValue }) => {
		let countryName;
		if (displayValue) {
			countryName = displayValue.substring(0, displayValue.indexOf(' ('));
		}

		// don't format at this point, wait for the componentWillReceiveProps
		// for it to have the resolved formats
		this.props.onValueChange({
			value: this.props.value,
			additionalInfo: {
				countryCode,
				formats: undefined,
				countryName,
				oldCountryCode
			}
		}); // reset formats until GraphQL is finished and sets new countryCode

		this.setPhoneFormat(countryCode);
	};

	onPhoneClick = event => {
		const {
			dispatch,
			phoneAddon,
			name,
			label,
			value,
			countryCode
		} = this.props;
		const fullNumber = countryCode ? `${countryCode}${value}` : value;
		if (isFunction(this.props.dispatch)) {
			dispatch('PHONE_ADDON_ACTION_CLICK', {
				phoneNumber: fullNumber,
				fieldName: name,
				fieldLabel: label
			});
		}
		if (isFunction(phoneAddon)) {
			phoneAddon(event, fullNumber, name, label);
		}
	};

	searchPhoneTerritories() {
		// should debounce for any call after componentDidMount
		// if e164, GraphQL query for full list of country codes?
		const { countryCodes } = this.props;
		if (countryCodes) {
			this.props.dispatch(FETCH_PHONE_TERRITORY, {
				queryConditions: 'active=true'
			});
		}
		// this might be a useful queryCondition to add:
		// sys_phone_territory.nameSTARTSWITH${searchValue}
	}

	setPhoneFormat = (countryCode = '1') => {
		this.props.dispatch(FETCH_PHONE_FORMATS, {
			queryConditions: `phone_territory.ccc=${countryCode}`,
			paginationLimit: AREA_CODE_MATCH_LIMIT
		});
	};

	componentWillReceiveProps({
		properties: {
			countryCode,
			countryCodeFormats,
			formatFetchStatus,
			countryCodeQueried
		},
		value
	}) {
		const oldFormatFetchStatus = this.props.properties.formatFetchStatus;
		const needsProcessing =
			formatFetchStatus === NEEDS_PROCESSING_FETCH_STATUS &&
			formatFetchStatus !== oldFormatFetchStatus;

		if (needsProcessing) {
			this.props.onValueChange(
				{
					value,
					additionalInfo: {
						countryCode: countryCodeQueried,
						formats: countryCodeFormats[countryCodeQueried]
					}
				},
				true
			);
			this.props.dispatch(FETCH_PHONE_PROCESSED, {});
		}

		// if we don't need processing + we have a new country-code, set the phone format
		else if (
			this.props.properties.countryCode !== countryCode &&
			size(this.props.properties.countryCodes) > 0
		) {
			// we're explicitly not using `getCountryCode` b/c this is the country code we want
			this.setPhoneFormat(countryCode);
		}
	}

	componentDidMount() {
		// TODO: make e164 flag to replace this
		if (size(this.props.countryCodes) > 0) {
			this.searchPhoneTerritories();
			this.setPhoneFormat(getCountryCode(this.props));
		}
	}

	shouldComponentUpdate(nextProps) {
		if (this.props.formatFetchStatus !== nextProps.formatFetchStatus)
			return true;
		const {
			countryCodes: nextCountryCodes,
			messages: nextMessage,
			...restOfNextProps
		} = nextProps;
		const {
			countryCodes: oldCountryCodes,
			messages: oldMessage,
			...restOfOldProps
		} = this.props;

		return !(
			shallowEqual(restOfNextProps, restOfOldProps) &&
			isEqual(nextCountryCodes, oldCountryCodes) &&
			isEqual(nextMessage, oldMessage)
		);
	}

	isDropdownDisabled = () =>
		isAttrTrue(this.props.readonly) ? true : undefined;

	render() {
		const inputProps = omit(
			this.props,
			'phoneAddon',
			'formatUsPhone',
			'initialValue',
			'countryCode',
			'countryCodes',
			'countryCodeQueried',
			'countryName',
			'defaultCode',
			'oldCountryCode',
			'countryCodeFormats',
			'formatFetchStatus',
			'formatFetchState'
		);
		const { countryCodes, territories } = this.props;
		const formattedPlaceHolder = getFormattedPlaceholder(this.props.formats);
		const defaultCode = getCountryCode(this.props);

		const allCodes = addChoiceValue([
			...addDisplayTrue(countryCodes),
			...normalizeQueryCountryCodes(territories)
		]);

		return (
			<ConnectedInput
				{...inputProps}
				type="tel"
				// Temporary fix for london
				ignoreDropdown={true}
				placeholder={
					isAttrTrue(inputProps.readonly) ? '' : formattedPlaceHolder
				}
				displayValue={this.props.value}
			>
				{size(countryCodes) > 0 && (
					<sn-record-choice-search
						slot="left"
						className="e164-choices"
						componentId={`${this.props.componentId}_phone_e164_choice`}
						name={`${name}_phone_e164_choice`}
						displayValue={addPlusSign(defaultCode)}
						choices={allCodes}
						onValueChange={this.onSelectChange(defaultCode)}
						onBlur={this.props.onBlur}
						disabled={this.isDropdownDisabled()}
					/>
				)}
			</ConnectedInput>
		);
	}
}

const stripSeparators = phoneNumber =>
	phoneNumber.replace(PhoneFormatter.phoneSeparatorRegex, '');

const numberMatch = (num1, num2) => {
	if (!num1 || !num2) return false;
	return stripSeparators(num1) === stripSeparators(num2);
};

export function parsing(value, props, state = {}) {
	if (numberMatch(value, props.originalValue))
		return { value: props.originalValue };
	const {
		countryCodes,
		properties: { countryCodeFormats }
	} = props;
	const { countryName } = state;
	if (size(countryCodes) < 1) {
		const parsedObject = PhoneFormatter.parse(value);
		return parsedObject;
	}
	const countryCode = getCountryCode(props, state);
	const formats = countryCodeFormats && countryCodeFormats[countryCode];
	return PhoneFormatter.e164parse(value, countryName, formats, countryCode);
}

export function formatting(value, props, state = {}) {
	const countryCodes = get(props, 'countryCodes');
	const { countryName, formats, oldCountryCode } = state;
	const defaultCode = getCountryCode(props, state);

	if (size(countryCodes) < 1) {
		return PhoneFormatter.format(value);
	}

	return PhoneFormatter.e164format(
		value,
		defaultCode,
		formats,
		countryName,
		oldCountryCode
	);
}
const ConnectedPhone = withState(Phone, {
	parse: parsing,
	format: formatting,
	stopBubbling: ['SN_RECORD_INPUT_CONNECTED']
});

ConnectedPhone.actionHandlers = actionHandlers;

ConnectedPhone.style = SNInput.style + ' ' + style;
ConnectedPhone.propTypes = {
	...omit(SNInput.propTypes, ['value']),
	/**
	 * Current country set on field in database
	 */
	countryCode: PropTypes.string,
	declarativeUiActions: PropTypes.array,
	formData: PropTypes.object,
	/**
	 * Declarative Actions Props
	 */
	declarativeActionsProps: PropTypes.object,

	/**
	 * Dictionary values and attributes for the field
	 */
	dictionary: PropTypes.object,

	/**
	 * Display value for the input.
	 */
	displayValue: PropTypes.string,

	/**
	 * Initial value for the input.
	 */
	initialValue: PropTypes.string,

	/**
	 * Callback function that is called as the user interacts with the control,
	 * fired as soon as user interaction has begun. It is not guaranteed to be
	 * the final or complete value from the user; use `onValueChange` for that.
	 *
	 * @param {SyntheticEvent} event The react event that triggered the change.
	 * This *can* be null when the value has been changed programatically.
	 *
	 * @param {String} name The name of the component. This will match the value
	 * of the `name` component prop.
	 *
	 * @param {Any} value The new value.
	 */
	onStagedValueChange: PropTypes.func,

	/**
	 * Callback function that is called as the user finishes interacting with
	 * the control.
	 *
	 * @param {SyntheticEvent} event The react event that triggered the change.
	 * This *can* be null when the value has been changed programatically.
	 *
	 * @param {String} name The name of the component. This will match the value
	 * of the `name` component prop.
	 *
	 * @param {Any} value The new value.
	 */
	onValueChange: PropTypes.func,

	/**
	 * Toggle the default formatting behavior for numbers that match us phone numbers
	 */
	formatUsPhone: PropTypes.bool,

	/**
	 * Phone addon flag to show/hide phone icon. This can be either a boolean or function.
	 * If its a function, clicking the phone icon will dispatch the event AND call the function
	 * as a callback.
	 */
	phoneAddon: PropTypes.oneOfType([
		PropTypes.func,
		PropTypes.bool,
		PropTypes.string
	]),

	/**
	 * List of available country codes that the user can choose from.
	 */
	countryCodes: PropTypes.arrayOf(
		PropTypes.shape({
			/**
			 * User-friendly version of the country code
			 */
			name: PropTypes.string,
			/**
			 * Display the option without need for searching
			 */
			display: PropTypes.boolean,
			/**
			 * Value for the country code
			 */
			code: PropTypes.string
		})
	),

	/*
	 * The default code to use for phone e164 fields
	 * TODO: hook this up for use in the component
	 */
	defaultCode: PropTypes.string,

	formatFetchState: PropTypes.string,
	formats: PropTypes.array,
	countryCodeQueried: PropTypes.string,
	countryCodeFormats: PropTypes.object,
	formatFetchStatus: PropTypes.string,
	originalValue: PropTypes.string
};

ConnectedPhone.defaultProps = {
	countryCodes: [],
	dispatch: () => {},
	displayValue: '',
	initialValue: ''
};

function addChoiceValue(countryCodes) {
	return map(countryCodes, country => {
		return {
			...country,
			displayValue: country.name + ' (+' + country.code + ')'
		};
	});
}

function addDisplayTrue(countryCodes) {
	return map(countryCodes, country => ({ ...country, display: true }));
}

function normalizeQueryCountryCodes(countries) {
	return map(countries, country => ({
		code: country.ccc.value,
		name: country.name.value,
		value: country.ccc.value
	}));
}

export default ConnectedPhone;
