import React from 'react';
import PropTypes from 'prop-types';
import { map, toString, find, cloneDeep, omit, some, isEqual } from 'lodash';
import { SNChoice } from '@devsnc/sn-form-controls';
import { withValueChangeCallbacks } from '@devsnc/sn-controls-common';
import {
	getEncodedRecord,
	getSerializedChanges
} from '@devsnc/sn-controls-common';
import actionHandlers, { FETCH_DATA } from './actionHandlers';

import { t } from 'sn-translate';

export function applyOptionStack(choices, optionStack) {
	if (!optionStack || optionStack.length == 0) {
		// No optionStack to apply so just return the choices
		return choices;
	}

	let newChoices = cloneDeep(choices);
	if (!newChoices) {
		newChoices = [];
	}
	optionStack.forEach(item => {
		switch (item.operation) {
			case 'add': {
				if (find(newChoices, c => c.displayValue === item.label)) {
					// validate that duplicate choice label is not being added.
					return;
				}

				let newOption = {
					displayValue: item.label,
					value: item.value
				};
				if (typeof item.index === 'undefined') {
					newChoices.push(newOption);
				} else {
					newChoices.splice(item.index, 0, newOption);
				}
				break;
			}
			case 'remove': {
				// normalize comparison to string values
				let itemValue = String(item.value);
				for (let i = 0, iM = newChoices.length; i < iM; i++) {
					let optionValue = String(newChoices[i].value);
					if (optionValue !== itemValue) {
						continue;
					}
					newChoices.splice(i, 1);
					break;
				}
				break;
			}
			case 'clear': {
				newChoices = [];
				break;
			}
			default: {
				break;
			}
		}
	});
	return newChoices;
}

const LOADING_DISPLAY_VALUE = `${t('LOADING...')}`;

export class ConnectedChoice extends React.PureComponent {
	static componentName = 'sn-record-choice-connected';
	constructor(props) {
		super(props);

		const { dictionaryAttributes = {} } = this.props;
		const refQualElements = dictionaryAttributes['refQualElements']
			? this.getRefQualElements(dictionaryAttributes['refQualElements'])
			: {};
		// Dependent fields might be constructed after the parent field value has
		// already changed. In these cases, the `choices` for the dependent field
		// correspond to the choices for the old parent field value, not the
		// current value. Need to immediately fetch up-to-date choices.
		//
		// When the parent value hasn't changed before this field has been
		// constructed, the `dependentValue` prop remains undefined so `props.choices`
		// contains accurate entries.
		const dependentChoiceData =
			props.dependentField &&
			(props.dependentFieldValue || props.dependentValue)
				? this.fetchDependentChoices(props)
				: {};

		this.state = {
			value: toString(this.props.initialValue),
			displayValue: toString(this.props.initialDisplayValue),
			valueCandidate: undefined,
			displayValueCandidate: undefined,
			isLoading: false,
			choices: applyOptionStack(props.choices, props.optionStack),
			refQualElements,
			...dependentChoiceData
		};
	}

	componentWillReceiveProps(newProps) {
		let {
			dependentField,
			dependentFieldValue,
			dependentValue,
			optionStack,
			choices,
			initialValue,
			initialDisplayValue,
			isLoading,
			formData
		} = newProps;

		const refQualElementsAttribute = this.props.dictionaryAttributes
			? this.props.dictionaryAttributes['refQualElements']
			: undefined;
		let changedField;
		if (refQualElementsAttribute) {
			const { serializedChanges = '' } = formData;
			changedField = this.getChangedFieldInSerializedChanged(
				serializedChanges,
				this.state.refQualElements
			);

			if (changedField) {
				const newRefQualElements = {
					...this.state.refQualElements,
					[changedField.name]: {
						name: changedField.name,
						value: changedField.value
					}
				};
				this.setState({ refQualElements: newRefQualElements });
			}
		}
		dependentValue = dependentFieldValue || dependentValue;
		const oldDependentValue =
			this.props.dependentFieldValue || this.props.dependentValue;

		if (
			(dependentField && dependentValue !== oldDependentValue) ||
			(refQualElementsAttribute && changedField)
		) {
			this.setState(this.fetchDependentChoices(newProps));
			return;
		}
		/*isLoading is actually passed in & its different than the state when we fetched
		 * & the choices passed are differrent than the empty state choices
		 * & the choices are different than what given on load
		 * & But sometimes the choices given are the same as the props
		 * but only for the none-choice.
		 */
		const justStoppedLoading =
			isLoading !== undefined &&
			isLoading !== this.state.isLoading &&
			!isEqual(choices, this.state.choices) &&
			(!isEqual(choices, this.props.choices) ||
				(choices.length == 1 &&
					choices[0].value === '' &&
					this.props.choices.length === 1 &&
					this.props.choices[0].value === ''));

		if (
			!this.props.forceUncontrolled &&
			(initialValue !== this.state.value ||
				initialDisplayValue !== this.state.displayValue)
		) {
			// only set state if the prop is a valid choice
			const hasChoiceMatch = some(this.state.choices, ['value', initialValue]);
			if (!(dependentField || refQualElementsAttribute) || hasChoiceMatch) {
				this.setState({
					value: initialValue === undefined ? '' : initialValue,
					displayValue: initialDisplayValue
				});
			} else {
				// if the prop isn't valid, remember what it was trying to set and make
				// the prop match the state in componentDidUpdate
				this.setState({
					valueCandidate: initialValue === undefined ? '' : initialValue,
					displayValueCandidate: initialDisplayValue
				});
			}
		}

		const newChoices = applyOptionStack(choices, optionStack);
		if (
			justStoppedLoading ||
			(!this.state.isLoading && !isEqual(newChoices, this.state.choices))
		) {
			this.setState({ choices: newChoices, isLoading });
		}
	}

	componentDidUpdate(prevProps, prevState) {
		const {
			valueCandidate,
			displayValueCandidate,
			choices,
			value,
			displayValue,
			isLoading
		} = this.state;
		if (
			valueCandidate === this.props.initialValue ||
			(prevState.valueCandidate !== valueCandidate &&
				valueCandidate !== undefined)
		) {
			// just remembered a value, so let's make props match our state (which is still correct)
			this.props.onValueChange({
				value: value,
				displayValue: displayValue
			});
		}

		const justStoppedLoading = isLoading !== prevState.isLoading && !isLoading;

		if (justStoppedLoading) {
			const hasChoiceMatch = some(choices, { value: value });
			const hasRememberedMatch =
				valueCandidate !== undefined &&
				some(choices, { value: valueCandidate });
			if (hasRememberedMatch) {
				// values set by script that become valid after a fetch should be set
				this.selectValue({
					value: valueCandidate,
					displayValue: displayValueCandidate
				});
			} else if (!hasChoiceMatch) {
				// Clear out the current value if the current value is no longer available in choices
				this.selectValue({
					value: '',
					displayValue: this.getDisplayValue(choices, '', '')
				});
			}
		}
	}

	/**
	 * Dependent fields are responsible for maintaining their own list of
	 * `choices` any time the parent value changes.
	 *
	 * Manage GraphQL call and figure out correct state. Note that we can't call
	 * `this.setState` anywhere here because we might be coming from the
	 * constructor, and doing that makes React sad. All new state is returned as
	 * an object instead.
	 */
	fetchDependentChoices = props => {
		const {
			dispatch,
			recordSysId,
			name,
			tableName,
			dependentField,
			dependentFieldValue,
			dependentValue
		} = props;

		if (dispatch) {
			const payload = {
				sys_id: recordSysId,
				field: name,
				table: tableName,
				serialized_changes: getSerializedChanges(props),
				encoded_record: getEncodedRecord(props.formData)
			};
			dispatch(FETCH_DATA, payload, { stopPropagation: true });

			return {
				choices: [],
				isLoading: true
			};
		}

		return {
			choices: []
		};
	};

	selectValue = ({ value, displayValue }) => {
		this.setState(
			{
				value,
				displayValue,
				valueCandidate: undefined,
				displayValueCandidate: undefined
			},
			() => this.props.onValueChange({ value, displayValue })
		);
	};

	// Returns either the displayValue or the displayValue of the choice with the matching value
	getDisplayValue(choices, value, displayValue) {
		const selected = find(choices, c => c.value === value);
		if (selected && selected.displayValue) return selected.displayValue;
		else if (displayValue) return displayValue;

		return '';
	}

	render() {
		// If we have local choices (from GraphQL response), use those.
		// Otherwise fall back to choices from parent
		const choices = this.state.choices || this.props.choices;
		const { value, displayValue } = this.state;
		const computedDisplayValue = this.getDisplayValue(
			choices,
			value,
			displayValue
		);

		const attrs = omit(this.props, ['onValueChange']);

		return (
			<sn-record-choice
				{...attrs}
				onValueChange={this.selectValue}
				choices={choices}
				value={this.state.value}
				displayValue={computedDisplayValue}
				className={`context-${this.props.context}`}
			/>
		);
	}

	/**
	 *  Parse the value of the attribute and build an object keyed by the value in the attribute
	 * Sets the value to the name of the field plus some characters as we dont know what the value
	 * is set to on first load.
	 * @param {string} refQulElemAttribute the value of the attribute. It might be plural ';' separated
	 */
	getRefQualElements = refQulElemAttribute => {
		let fields = refQulElemAttribute.split(';');
		let elements = fields.reduce((acc, current) => {
			acc[current] = {
				value: current + '- -',
				name: current
			};
			return acc;
		}, {});
		return elements;
	};
	/**
	 * Find the field the changed within serializedChanges
	 * @param {string} serializedChangesString String from formData passed by forms
	 * @param {object} attributeFields Object key by field name
	 * @returns {object} The object found in the parsed serializedChangesString that matches the attribute field
	 */
	getChangedFieldInSerializedChanged = (
		serializedChangesString,
		attributeFields
	) => {
		let serializedChanges = JSON.parse(serializedChangesString);
		return find(serializedChanges, changedField => {
			try {
				let potentialChange = find(
					attributeFields,
					({ name }) => name == changedField.name
				);
				//Nothing found from this serializedChange
				if (!potentialChange) return false;
				return potentialChange.value !== changedField.value;
			} catch (e) {
				/* fall through and return false */
			}
		});
	};
}

ConnectedChoice.propTypes = {
	...omit(SNChoice.propTypes, ['value', 'displayValue']),

	/**
	 * Initial value for the choice
	 */
	initialValue: PropTypes.string,

	/**
	 * Initial display value for the choice
	 */
	initialDisplayValue: PropTypes.string,

	/**
	 * Callback function that is called as the user finishes interacting with
	 * the control.
	 *
	 * @param {SyntheticEvent} event The react event that triggered the change.
	 * This *can* be null when the value has been changed programatically.
	 *
	 * @param {String} name The name of the component. This will match the value
	 * of the `name` component prop.
	 *
	 * @param {Any} value The new value.
	 */
	onValueChange: PropTypes.func,

	tableName: PropTypes.string,

	recordSysId: PropTypes.string,

	dependentValue: PropTypes.string,

	dependentField: PropTypes.string,

	dependentFieldValue: PropTypes.string,

	optionStack: PropTypes.array,

	formData: PropTypes.object,

	forceUncontrolled: PropTypes.bool,

	dictionaryAttributes: PropTypes.object
};
ConnectedChoice.actionHandlers = actionHandlers;

export default withValueChangeCallbacks(
	{ cache: false, stopBubbling: ['SN_RECORD_CHOICE'] },
	ConnectedChoice
);
