import {
	nowPopOverOpenedEffect,
	fetchEffect,
	fetchSuccessEffect,
	fetchFailureEffect,
	fetchLeafEffect,
	fetchLeafSuccessEffect,
	fetchLeafFailureEffect,
	pathUpdatedHandler,
	dotWalkLeafHandler,
	propertyChangeHandler,
	updateBreadCrumbHandler
} from './effects';
import {
	POPOVER_OPENED,
	DOT_WALK_PATH_UPDATED,
	DOT_WALK_TRIGGERED,
	DOT_WALK_BREADCRUMB_PATH_UPDATED,
	FETCH_DATA,
	FETCH_DATA_SUCCESS,
	FETCH_DATA_FAILURE,
	FETCH_LEAF_DATA,
	FETCH_LEAF_DATA_SUCCESS,
	FETCH_LEAF_DATA_FAILURE
} from './constants';
import { actionTypes } from '@servicenow/ui-core';
import { effectProxy } from '@devsnc/sn-controls-common';

const { COMPONENT_PROPERTY_CHANGED } = actionTypes;

export default {
	[DOT_WALK_PATH_UPDATED]: pathUpdatedHandler,
	[DOT_WALK_BREADCRUMB_PATH_UPDATED]: updateBreadCrumbHandler,
	[DOT_WALK_TRIGGERED]: dotWalkLeafHandler,

	[COMPONENT_PROPERTY_CHANGED]: propertyChangeHandler,

	[POPOVER_OPENED]: {
		effect: nowPopOverOpenedEffect,
		stopPropagation: true
	},

	[FETCH_DATA]: {
		effect: effectProxy(fetchEffect),
		stopPropagation: true
	},
	[FETCH_DATA_SUCCESS]: {
		effect: fetchSuccessEffect,
		stopPropagation: true
	},
	[FETCH_DATA_FAILURE]: {
		effect: fetchFailureEffect,
		stopPropagation: true
	},

	[FETCH_LEAF_DATA]: {
		effect: effectProxy(fetchLeafEffect),
		stopPropagation: true
	},
	[FETCH_LEAF_DATA_SUCCESS]: {
		effect: fetchLeafSuccessEffect,
		stopPropagation: true
	},
	[FETCH_LEAF_DATA_FAILURE]: {
		effect: fetchLeafFailureEffect,
		stopPropagation: true
	}
};
