import { createCustomElement } from '@servicenow/ui-core';

import '@servicenow/now-dot-walk';
import '@devsnc/sn-form-controls';
import {
	getEncodedRecord,
	getSerializedChanges
} from '@devsnc/sn-controls-common';

import actionHandlers from './actionHandlers';
import eventHandlers from './eventHandlers';
import { FIELD_NAME } from './constants';
import { isAttrTrue } from '@devsnc/sn-controls-common';
import { FETCH_DATA } from './constants';
import styles from './styles.scss';

const view = (state, { dispatch, updateProperties }) => {
	const { dataModel = {}, error, placeholder } = state;
	let { opened } = state;
	let { path = [], metadata = {}, leafPathSource = '' } = dataModel;
	const {
		readonly,
		dictionaryAttributes: { allowDotWalking = false },
		displayValue,
		displayValueWithPath,
		isReadOnly,
		liveUpdate
	} = state.properties;

	if (state.liveUpdate) {
		opened = false;
		//check prop liveUpdate
		if (liveUpdate) {
			//reset the liveUpdate prop
			updateProperties({
				liveUpdate: false
			});
			let {
				name,
				referringRecordId,
				formData = {},
				referringTable: parentTableName,
				dependentFieldValue: tableName,
				value: path = ''
			} = state.properties;

			const payload = {
				parentTableName,
				fieldName: name,
				recordSysId: referringRecordId,
				serializedChanges: getSerializedChanges(state.properties),
				encodedRecord: getEncodedRecord(formData),
				tableName,
				path
			};
			dispatch(FETCH_DATA, payload, { stopPropagation: true });
		}
	}

	return (
		<now-popover manageOpened={true} opened={opened} hideTail>
			<sn-record-choice
				{...state.properties}
				slot="trigger"
				class={{ 'sn-record-choice-field-trigger': true }}
				aria-haspopup="listbox"
				aria-expanded={opened ? 'true' : 'false'}
				placeholder={placeholder}
				displayValue={
					isAttrTrue(readonly) && allowDotWalking && displayValueWithPath
						? displayValueWithPath
						: displayValue
						? displayValue
						: '-'
				}
				readonly={isAttrTrue(readonly) ? readonly : isReadOnly}
			/>
			<now-dot-walk
				slot="content"
				dotWalkPath={path}
				metadata={metadata}
				leafPathSource={leafPathSource}
				error={error}
			/>
		</now-popover>
	);
};

/**
 *
 *  Field Name Type Component
 *```jsx
 *	<sn-record-fieldname-connected
 *		label="Field Name"
 *		value={state.value}
 *		displayValue={state.displayValue}
 *	/>
 *
 *```
 * @seismicElement sn-record-fieldname-connected
 * @summary *  Field Name Type. Used internally.
 * */
createCustomElement(FIELD_NAME, {
	view,
	styles,
	initialState: {
		opened: false,
		liveUpdate: false
	},
	properties: {
		/**
		 * Additional content describing the field, usually to provide further
		 * instruction to the user.
		 * @type {string}
		 */
		description: {},

		/**
		 * Additional messages used to give status information on a field, usually
		 * to provide error messages or warnings
		 * @type {array}
		 */
		messages: { default: [] },

		/**
		 * Flag indicating whether there is an error
		 * @type {string | boolean }
		 */
		error: { default: '' },

		/**
		 * Specifies whether the current value is not valid.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true. Used by the glide form controls demo
		 * @type {string | boolean}
		 * @private
		 */
		invalid: {},

		/**
		 * It's a label! All form controls need a label!
		 * @type {string}
		 */
		label: {},

		/**
		 * The language for the character used in the input field
		 * @type {string}
		 */
		language: {},

		/**
		 * Name for the component. Any events dispatched by the component will use this name.
		 * @type {string}
		 */
		name: {},

		/**
		 * Specifies that the user cannot modify the value of the component.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 * @type {string | boolean}
		 */
		readonly: { default: false },

		/**
		 * Whether or not the user must fill in a value for this component.
		 * Can be specified as a boolean or the string "true"
		 * @type {string | boolean}
		 */
		required: { default: false },

		/**
		 * Specifies that the field should be rendered right-to-left.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 * @type {string | boolean}
		 * @private
		 */
		reversed: { default: false },

		/**
		 * The value of all the fields inside this components. This is a string dilimtad by `^` chars.
		 * @type {string}
		 */
		value: { schema: { type: 'string' }, default: '' },

		/**
		 * DisplayValue for current component.
		 * @type {string}
		 */
		displayValue: { schema: { type: 'string' }, default: '' },

		/**
		 * Callback used when the value has changed. Very similar to an `onChange`
		 * event, but this may also fire when the component formats its value
		 * onBlur.
		 *
		 * @param {object} event - The event payload
		 * @param {string} event.name - The name of the component that triggered the event
		 * @param {any}    event.value - The new value for the component
		 * @param {string} event.displayValue - The human-readable represenation of the value
		 *
		 * @param {object} event.error - If the current value is invalid, this propery will be set. When the current value is valid, this property will be null.
		 * @param {string} event.error.type - Error type
		 * @param {string} event.error.message - Description of why the value is invalid
		 * @type {function}
		 * @private
		 */
		onValueChange: {},

		/**
		 * Callback used when an intermediate value has changed.
		 * @type {function}
		 * @private
		 */
		onStagedValueChange: {},

		/**
		 * Optional name of the dependent field
		 *
		 * @type {string}
		 */
		dependentField: { schema: { type: 'string' }, default: '' },

		/**
		 * Optional value of the dependent field
		 *
		 * @type {string}
		 */
		dependentFieldValue: { schema: { type: 'string' }, default: '' },
		dependentValue: { schame: { type: 'string' }, default: '' },

		/**
		 * Optional value of the dependent field label
		 *
		 * @type {string}
		 */
		dependentFieldLabel: { schema: { type: 'string' }, default: '' },

		/**
		 * Dictionary attributes to set and configure the query for table names
		 */
		dictionaryAttributes: { schema: { type: 'object' } },

		/**
		 * Content to display when there is no suitable display value.
		 * @type {string}
		 */
		placeholder: {},

		/**
		 * Flag to indicate if it's a liveUpdate
		 */
		liveUpdate: {
			schema: { type: 'boolean' }
		},

		/**
		 * This is the Sys ID of the record that has the field in it.
		 */
		referringRecordId: { schema: { type: 'string' } },

		/**
		 * This is the table for the record that has the field in it.
		 */
		referringTable: { schema: { type: 'string' } },

		/**
		 * formData object containing glide form data
		 */
		formData: { schema: { type: 'object' } },

		/**
		 * This is the complete display value with dot walk path separated by > .
		 */
		displayValueWithPath: { schema: { type: 'string' } },

		isReadOnly: { schema: { type: 'boolean' } }
	},
	actionHandlers,
	eventHandlers
});
