import { POPOVER_OPENED } from './constants';
import { isAttrTrue } from '@devsnc/sn-controls-common';

function getFocusedElement(host) {
	return host.shadowRoot.activeElement;
}

function getTriggerElement(host) {
	return host.shadowRoot.querySelector('.sn-record-choice-field-trigger');
}

export default [
	{
		events: ['keydown'],
		effect: ({
			action,
			host,
			dispatch,
			updateState,
			state: {
				properties: { readonly, isReadOnly: dependentValueEmpty }
			}
		}) => {
			const event = action.payload.event;
			const focused = getFocusedElement(host);
			const trigger = getTriggerElement(host);
			switch (event.key) {
				case 'ArrowDown':
				case 'ArrowUp':
				case 'Enter':
				case ' ':
					const isReadOnly = isAttrTrue(readonly)
						? readonly
						: dependentValueEmpty;
					if (!host.disabled && focused === trigger && !isAttrTrue(isReadOnly)) {
						event.preventDefault();
						dispatch(POPOVER_OPENED, {value: true} );
					}
					break;
				case 'Escape':
				case 'Tab':
					if (focused !== trigger) {
						event.preventDefault();
						updateState({ opened: false });
						trigger.focus();
					}
					break;
			}
		}
	}
];
