import { omit, get, some, isEmpty } from 'lodash';
import { t } from 'sn-translate';

import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import {
	adaptCallbackForLegacyAPI,
	isAttrTrue
} from '@devsnc/sn-controls-common';

import actionHandlers from './actionHandlers';
import {
	QUERY_DATA_PATH,
	DEPENDENT_FIELD,
	DEPENDENT_VALUE,
	DEPENDENT_FIELD_LABEL
} from './constants';

const getOptions = data => {
	if (data)
		return data.map(
			({ sysId: value, referenceData: [{ value: displayValue }] }) => ({
				value,
				displayValue
			})
		);

	return [];
};

const pushMissingTable = (messages, label) => {
	const message = t('Select a {0} before modifying this field.', label);
	return [
		...messages,
		{
			type: 'info',
			message
		}
	];
};

/**
 * Create the Document ID view
 */
const view = (
	{
		normalizedDisplayValue,
		properties,
		properties: {
			recordSysId,
			fieldName,
			tableName,
			resultLimit,
			formData,
			readonly,
			messages,
			value,
			onValueChange: parentOnValueChange,
			[DEPENDENT_FIELD_LABEL]: dependentFieldLabel,
			[DEPENDENT_FIELD]: dependentField,
			[DEPENDENT_VALUE]: dependentValue
		}
	},
	{ updateState }
) => {
	const mapResults = data => {
		let results = get(data, QUERY_DATA_PATH, {});
		return {
			recents: getOptions(results.referenceRecentDataList),
			options: getOptions(results.referenceDataList),
			totalCount: results.matchesCount
		};
	};

	const getVariables = inputVal => {
		return {
			sys_id: get(formData, 'isNewRecord') ? '-1' : recordSysId,
			table: tableName,
			field: fieldName,
			paginationLimit: resultLimit,
			chars: inputVal.length > 0 ? inputVal : '**',
			dependentValue
		};
	};

	// We want to intercept the onValueChange so that we can set displayValue here
	// as opposed to listening to prop changes which might give us incorrect display
	// values when things are in flux
	const onValueChange = args => {
		const { displayValue } = args;
		updateState({ normalizedDisplayValue: displayValue });
		adaptCallbackForLegacyAPI(parentOnValueChange)(args);
	};

	const additionalProps = omit(properties, ['resultLimit']);

	// We have to "hide" the values when one of these is empty, so that we don't get the "sys-id without displayValue" warning created by the sn-glide-query-base
	const emptyDeps = some([dependentField, dependentValue], isEmpty);
	const isReadonly = isAttrTrue(readonly) || emptyDeps;
	const hideValues = emptyDeps || !normalizedDisplayValue;
	const addMissingMessage =
		!isAttrTrue(readonly) &&
		isEmpty(dependentValue) &&
		!isEmpty(dependentFieldLabel);

	if (hideValues) {
		value = '';
		normalizedDisplayValue = '';
	}

	if (addMissingMessage) {
		messages = pushMissingTable(messages, dependentFieldLabel);
	}

	return (
		<sn-glide-query-base
			{...additionalProps}
			value={value}
			displayValue={normalizedDisplayValue}
			readonly={isReadonly}
			messages={messages}
			mapResults={mapResults}
			getVariables={getVariables}
			onValueChange={onValueChange}
		/>
	);
};

createCustomElement('sn-record-document-id-connected', {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {
		normalizedDisplayValue: ''
	},
	actionHandlers,
	properties: {
		/**
		 * It's a label! All form controls need a label!
		 * @type {string}
		 */
		label: { schema: { type: 'string' } },

		/**
		 * Name for the component. Any events dispatched by the component will use this name.
		 * @type {string}
		 */
		name: { schema: { type: 'string' } },

		/**
		 * Name of table this field is on.
		 * @type {string}
		 * @private
		 */
		tableName: { schema: { type: 'string' }, required: true },

		/**
		 * Name of the field.
		 * @type {string}
		 * @private
		 */
		fieldName: { schema: { type: 'string' }, required: true },

		/**
		 * The sys_id of the current record where the field is located
		 * @type {string}
		 * @private
		 */
		recordSysId: { schema: { type: 'string' } },

		/**
		 * The value of the field.
		 * @type {string}
		 */
		value: { schema: { type: 'string' }, default: '' },

		/**
		 * DisplayValue for current component.
		 * @type {string}
		 */
		displayValue: { schema: { type: 'string' } },

		/**
		 * Specifies that the user cannot modify the value of the component.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 * @type {string | boolean}
		 */
		readonly: { schema: { type: ['string', 'boolean'] }, default: false },

		/**
		 * Whether or not the user must fill in a value for this component.
		 * Can be specified as a boolean or the string "true"
		 * @type {string | boolean}
		 */
		required: { schema: { type: ['string', 'boolean'] }, default: false },

		/**
		 * Number of suggestions to display.
		 * @type {number}
		 */
		resultLimit: { schema: { type: 'number' }, default: 25 },

		/**
		 * Callbacks for Forms consumption
		 * @type {function}
		 * @private
		 */
		onValueChange: { schema: { type: 'function' } },

		/**
		 * Specifies that the field should be rendered right-to-left.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 * @type {string | boolean}
		 * @private
		 */
		reversed: { schema: { type: ['string', 'boolean'] }, default: false },

		/**
		 * Flag to show when a graphQL request is in progress.
		 * @type {boolean}
		 * @private
		 */
		isSearching: { schema: { type: 'boolean' } },

		/**
		 * Results from a graphQL request
		 * @type {object}
		 * @private
		 */
		results: {
			schema: { type: 'object' }
		},

		/**
		 * Array of message objects that must contain type and message itself.
		 *
		 * @type {Array<{type: ("error"|"warning"|"success"|"liveUpdate"|"info"|"suggestion"), type:Array<string,Object<{string, getMessage:function}>> }>}
		 */
		messages: { schema: { type: 'object' } },

		/**
		 * Dictionary attributes for the document id component.
		 * @type {object}
		 * @private
		 */
		dictionaryAttributes: { schema: { type: 'object' } },

		/**
		 * Props for declarative actions associated with this field.
		 * @type {object}
		 * @private
		 */
		declarativeActionsProps: { schema: { type: 'object' } },

		/**
		 * Holds serialized changes.
		 * @type {object}
		 * @private
		 */
		formData: { schema: { type: 'object' } },

		/**
		 * Name of the field that this component depends on.
		 * @type {string}
		 * @private
		 */
		dependentField: { schema: { type: 'string' } },

		/**
		 * The value of the dependent field. AKA the referenced table.
		 * @type {string}
		 * @private
		 */
		dependentValue: { schema: { type: 'string' }, required: true },

		/**
		 * The value of the dependent field. AKA the referenced table.
		 * @type {string}
		 * @private
		 */
		dependentValueLabel: { schema: { type: 'string' }, required: true },

		/**
		 * Label of the field that this component depends on.
		 * @type {string}
		 * @private
		 */
		dependentFieldLabel: { schema: { type: 'string' }, required: true },

		/**
		 * Flag to indicate if it's a liveUpdate
		 * @type {boolean}
		 * @private
		 */
		liveUpdate: {
			schema: { type: 'boolean' }
		}
	}
});
