import { createCustomElement } from '@servicenow/ui-core';
import registerComponents from '@devsnc/library-register-components';
import { preUserData } from 'sn-uxpage-presource';

import ConnectedChoice from '../connectedchoice/ConnectedChoice';
import '@devsnc/sn-record-input-connected';
import '@servicenow/now-record-number';
import Email from '../email/Email';
import ConnectedPhone from '../connectedphone/ConnectedPhone';
import ConnectedReference from '../connectedreference/ConnectedReference';
import '@servicenow/now-record-field-lookup-connected';
import ConnectedCurrency from '../connectedcurrency/ConnectedCurrency';
import '@devsnc/sn-record-url';
import '@servicenow/now-record-ip-address';
import '@servicenow/now-record-html-editor';
import '@devsnc/sn-record-file-attachment';
import '../template-value'; //sn-record-template-value-connected
import '../connectedtablename/ConnectedTableName';
import '../connectedfieldname/ConnectedFieldName';
import '../connecteddocumentid/ConnectedDocumentId';
import GlideQueryBase from '@devsnc/sn-glide-query-base';

registerComponents(createCustomElement, {
	'sn-record-choice-connected': {
		component: ConnectedChoice
	},
	'sn-record-email': {
		component: Email
	},
	'sn-record-phone-connected': {
		component: ConnectedPhone,
		behaviors: [preUserData]
	},
	'sn-record-currency-connected': {
		component: ConnectedCurrency
	},
	'sn-record-reference-connected': {
		component: ConnectedReference,
		actionTypesToProps: { VALUE_SELECTED: 'addonValueSelected' }
	},
	'sn-glide-query-base': {
		component: GlideQueryBase
	}
});
