const isSupportedLocal = locale => {
	let length;
	try {
		length = Intl.NumberFormat.supportedLocalesOf([locale]).length;
	} catch (err) {
		return false;
	}
	return length > 0;
};

function normalizeFormat(locale) {
	switch (locale) {
		case 'pb':
			return 'pt-BR';
		case 'fq':
			return 'fr-CA';
		case 'zt':
			return 'zh-Hant';
		case 'xl':
			return 'en';
		default:
			return locale ? locale.replace(/[._]/, '-') : window.navigator.language;
	}
}

export default function fixLocale(locale) {
	const formatedLocale = normalizeFormat(locale);
	return isSupportedLocal(formatedLocale)
		? formatedLocale
		: window.navigator.language;
}
