import {
	queryBaseActionHandlers,
	queryBaseFetchDataEffectHandler
} from '@devsnc/sn-glide-query-base';
import { get } from 'lodash';

let query = `
query ($table: String!, $field: String!, $sys_id: String, $encodedRecord: String, $serializedChanges: String, $chars: String!, $ignoreRefQual: Glide_Boolean, $paginationLimit: Int, $paginationOffset: Int, $sortBy: String ) {
  GlideLayout_Query {
    referenceDataRetriever(
      tableName: $table,
      fieldName: $field,
      encodedRecord: $encodedRecord,
      serializedChanges: $serializedChanges,
      pagination: {limit: $paginationLimit, offset: $paginationOffset},
      ignoreTotalCount: true,
      sysId: $sys_id, chars: $chars,
      sysparm_ignore_ref_qual: $ignoreRefQual,
      sortBy: $sortBy ) {
      totalCount
      recentCount
      matchesCount
      referenceRecentDataList {
        sysId
        referenceData {
          key
          value
        }
      }
      referenceDataList {
        sysId
        referenceData {
          key
          value
        }
      }
    }
  }
}
`;

let variableList = [
	'table',
	'field',
	'sys_id',
	'encodedRecord',
	'serializedChanges',
	'chars',
	'ignoreRefQual',
	'paginationLimit',
	'paginationOffset',
	'sortBy'
];

const valueChangeHandler = {
	VALUE_CHANGED: ({ updateProperties, state, action }) => {
		const name = get(action.payload, 'name', '');
		if (name === state.properties.name) {
			updateProperties({
				focusInput: Date.now()
			});
		}

		return state;
	}
};

const queryBaseActionHandlersData = queryBaseActionHandlers(
	query,
	variableList
);
export const queryBaseFetchDataEffect = queryBaseFetchDataEffectHandler(
	query,
	variableList
);

export default {
	...queryBaseActionHandlersData,
	...valueChangeHandler
};
