import { first, map, tail, toString } from 'lodash';

/**
 * Return a filtered list of option objects
 * @param {array} data query result
 * @param {array} selected list of selected values
 * @return {array} list of option objects
 */
export function getResults(data, dictionarySorted) {
	const results = map(data, getOptionFromResult).filter(item => item.value);

	if (!dictionarySorted) {
		results.sort((a, b) =>
			toString(a.displayValue).localeCompare(toString(b.displayValue))
		);
	}
	return results;
}

/**
 * Return a option object from query result
 * @param {object} result result object
 * @return {object} option object
 */
export function getOptionFromResult({ referenceData, sysId } = {}) {
	const displayValues = map(referenceData, 'value');
	return {
		value: sysId,
		displayValue: first(displayValues),
		secondary: tail(displayValues)
	};
}
