import { createCustomElement } from '@servicenow/ui-core';
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom';
import actionHandlers from './actions';
import './field-selection';
import styles from './template-value.scss';
import '@servicenow/now-loader';
import * as FIELD_TYPE from '../base/FieldType';

export const COMPONENT_NAME = 'sn-record-template-value-connected';
const WORKSPACE_TYPES = Object.keys(FIELD_TYPE).map(key => FIELD_TYPE[key]);

const content = state => {
	const {
		isLoadingNewField,
		tempNewFieldLabel,
		table = '',
		choices = []
	} = state;
	const { readonly, formData } = state.properties;
	const fieldValues = state.fieldValues || [];

	return (
		<Fragment>
			{fieldValues.map((field, index) => (
				<sn-record-template-value-field-selection
					fieldName={field.fieldName}
					fieldLabel={field.fieldLabel}
					fieldProps={field.fieldProps}
					choices={choices}
					displayValue={field.displayValue}
					value={field.value}
					isLoading={field.isLoading}
					messages={field.messages}
					type={field.type}
					path={field.path}
					table={table}
					padBottom={true}
					key={`field-selection-${index}`}
					identifier={field.path || field.fieldName}
					isUnsupportedField={isUnsupported(field)}
					readonly={readonly}
					formData={formData}
				/>
			))}
			<sn-record-template-value-field-selection
				fieldLabel={tempNewFieldLabel}
				choices={choices}
				isNewField={true}
				isLoading={isLoadingNewField}
				// maintain consistent key to help maintain tab order when adding new field
				key={`field-selection-${fieldValues.length}`}
				identifier="newFieldCreator"
				table={table}
				readonly={readonly}
				formData={formData}
			/>
		</Fragment>
	);
};

const loading = () => (
	<div>
		<now-loader />
	</div>
);

const view = state => {
	const { label } = state.properties;
	const { isLoading } = state;

	return (
		<div>
			<label class={{ 'sn-control-label': true, 'template-value-label': true }}>
				{label}
			</label>
			{isLoading ? loading() : content(state)}
		</div>
	);
};

function isDotWalk(field) {
	return field.path.indexOf('.') > -1;
}

function isUnsupported(field) {
	return (
		!WORKSPACE_TYPES.includes(field.type) ||
		isDotWalk(field) ||
		field.shouldHide
	);
}

/**
 * This component allows you to collect and display field values for a given
 * table. These feilds can include dot walked fields. The values are local to
 * this component and not association with a record on the target table. This
 * field is meant to have a dependent field such as
 * `sn-record-tablename-connected` that provides the `table` name. This field
 * is also created to live in the form and not for general use.
 *
 * ```jsx
 * <sn-record-template-value-connected
 *   dependentField: 'table',
 *   dependentFieldValue: 'problem'
 * />
 * ```
 *
 * @seismicElement sn-record-template-value-connected
 * @summary Allows user to save multiple values and fields that corresponds with
 *  a tables field types. It also includes dot walked fields.
 */
createCustomElement(COMPONENT_NAME, {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {
		fieldValues: [],
		isLoading: false,
		isLoadingNewField: false,
		table: ''
	},
	styles,
	actionHandlers,
	properties: {
		/**
		 * Name identifier for the component.
		 * @type {string}
		 */
		name: { schema: { type: 'string' } },

		/**
		 * The label to display over the component.
		 */
		label: { schema: { type: 'string' } },

		/**
		 * The value of all the fields inside this components. This is a string dilimtad by `^` chars.
		 * @type {any}
		 */
		value: { schema: { type: 'string' } },

		/**
		 * The field whose value drives this component's table
		 */
		dependentField: { schema: { type: 'string' } },

		/**
		 * The value of the dependent field
		 */
		dependentValue: { schema: { type: 'string' } },

		/**
		 * This is the Sys ID of the record that has the template value field in it.
		 */
		referringRecordId: { schema: { type: 'string' } },

		/**
		 * This is the table for the record that has the template value field in it.
		 */
		referringTable: { schema: { type: 'string' } },

		/**
		 * This is a call back function that comes from forms. It is called when a value changes in the field.
		 */
		onValueChange: {},

		/**
		 * This is a call back function that comes from forms. This should be called as the user types but can cause performance issues if over used.
		 */
		onStagedValueChange: {},

		/**
		 * Specifies that the user cannot modify the value of the component.
		 * When passed as a string, "true" (case-sensitive) will be treated like
		 * boolean true.
		 * @type {string | boolean}
		 */
		readonly: { default: false },
		formData: {}
	},
	dispatches: {}
});

export default COMPONENT_NAME;
