import { get } from 'lodash';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { effectProxy } from '@devsnc/sn-controls-common';

const formatQuery = `
query ($queryConditions: String!, $paginationLimit: Int) {
  GlideRecord_Query {
    sys_phone_format(
			queryConditions: $queryConditions,
			pagination: {limit: $paginationLimit}
		) {
      _results {
        condition {
          value
        }
        format {
          value
        }
        pattern {
          value
        }
      }
    }
  }
}
`;

const territoryQuery = `
query ($queryConditions: String!) {
  GlideRecord_Query {
    sys_phone_territory(queryConditions: $queryConditions) {
      _results {
        name {
          value
        }
        ccc {
          value
        }
      }
    }
  }
}
`;

export const FETCH_PHONE_FORMATS_START = 'FETCH_PHONE_FORMATS_START';
export const FETCH_PHONE_FORMATS = 'FETCH_PHONE_FORMATS';
export const FETCH_PHONE_FORMATS_SUCCESS = 'FETCH_PHONE_FORMATS_SUCCESS';
export const FETCH_PHONE_TERRITORY = 'FETCH_PHONE_TERRITORY';
export const FETCH_PHONE_TERRITORY_SUCCESS = 'FETCH_PHONE_TERRITORY_SUCCESS';
export const FETCH_PHONE_PROCESSED = 'FETCH_PHONE_PROCESSED';

export const LOADING_FETCH_STATUS = 'LOADING_FETCH_STATUS';
export const NEEDS_PROCESSING_FETCH_STATUS = 'NEEDS_PROCESSING_FETCH_STATUS';
export const PROCESSED_FETCH_STATUS = 'PROCESSED_FETCH_STATUS';

const GRAPHQL_FORMAT_DATA_PATH =
	'data.GlideRecord_Query.sys_phone_format._results';

const GRAPHQL_TERRITORY_DATA_PATH =
	'data.GlideRecord_Query.sys_phone_territory._results';

export const phoneFormatGraphQLEffect = createGraphQLEffect(formatQuery, {
	variableList: ['queryConditions', 'paginationLimit'],
	startActionType: FETCH_PHONE_FORMATS_START,
	successActionType: FETCH_PHONE_FORMATS_SUCCESS
});

export const phoneTerritoryGraphQLEffect = createGraphQLEffect(territoryQuery, {
	variableList: ['queryConditions'],
	successActionType: FETCH_PHONE_TERRITORY_SUCCESS
});

export default {
	[FETCH_PHONE_FORMATS]: effectProxy(phoneFormatGraphQLEffect),
	[FETCH_PHONE_FORMATS_START]: ({ updateProperties }) => {
		updateProperties({
			formatFetchState: LOADING_FETCH_STATUS
		});
	},
	[FETCH_PHONE_FORMATS_SUCCESS]: ({ updateProperties, action }) => {
		const { payload, meta } = action;
		// TODO: Check error cases

		const formats = get(payload, GRAPHQL_FORMAT_DATA_PATH);
		const queryConditions = get(meta, 'options.variables.queryConditions');
		const countryCode = queryConditions.match(/\d{1,3}$/)[0];

		updateProperties({
			formats,
			countryCodeQueried: countryCode,
			countryCodeFormats: { [countryCode]: formats },
			formatFetchStatus: NEEDS_PROCESSING_FETCH_STATUS
		});
	},
	[FETCH_PHONE_TERRITORY]: effectProxy(phoneTerritoryGraphQLEffect),
	[FETCH_PHONE_TERRITORY_SUCCESS]: ({ updateState, action }) => {
		const { payload } = action.payload;
		const territories = get(payload, GRAPHQL_TERRITORY_DATA_PATH);
		updateState({
			territories
		});
	},
	[FETCH_PHONE_PROCESSED]: ({ updateProperties }) => {
		updateProperties({
			formatFetchStatus: PROCESSED_FETCH_STATUS
		});
	},
	['SN_CHOICE_SEARCH_DROPDOWN#ITEM_SELECTED']: ({ action }) => {
		action.stopPropagation();
	}
};
