import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { get, map } from 'lodash';

let query = `
query ($table: String!, $field: String!, $sys_id: String, $serialized_changes: String, $encoded_record: String) {
  GlideLayout_Query {
    choiceDataRetriever(tableName: $table, fieldName: $field, sysId: $sys_id, serializedChanges: $serialized_changes, encodedRecord: $encoded_record) {
      choice {
        value
        label
      }
    }
  }
}
`;

export const FETCH_DATA = 'CHOICE_FETCH_DATA';
const FETCH_DATA_SUCCESS = 'CHOICE_FETCH_DATA_SUCCESS';
const FETCH_DATA_FAILURE = 'CHOICE_FETCH_DATA_FAILURE';
const GRAPHQL_DATA_PATH = 'data.GlideLayout_Query.choiceDataRetriever.choice';

export default {
	[FETCH_DATA]: createGraphQLEffect(query, {
		variableList: [
			'table',
			'field',
			'sys_id',
			'serialized_changes',
			'encoded_record'
		],
		successActionType: FETCH_DATA_SUCCESS,
		errorActionType: FETCH_DATA_FAILURE
	}),

	[FETCH_DATA_SUCCESS]: ({ updateState, action }) => {
		const { payload } = action;
		if (payload.errors.length < 1) {
			const results = get(payload, GRAPHQL_DATA_PATH);
			const choices = map(results, choice => ({
				value: choice.value,
				displayValue: choice.label
			}));
			updateState({
				choices,
				isLoading: false
			});
		} else {
			updateState({
				choices: [],
				isLoading: false
			});
		}
	},

	[FETCH_DATA_FAILURE]: ({ updateState }) => {
		updateState({
			choices: [],
			isLoading: false
		});
	}
};
