// 3rd party imports
import React from 'react';
import PropTypes from 'prop-types';
import { omit, get, find } from 'lodash';

// Function imports

import {
	withValueChangeCallbacks,
	fieldPropTypes
} from '@devsnc/sn-controls-common';
import { getResults } from './utils';
import actionHandlers from './actionHandlers';

// Component imports
import GlideQueryBase from '@devsnc/sn-glide-query-base';

/**
 * Reference fields allow users to select a value from a dynamic list of
 * choices that point to another field in the database. For a more general
 * approach (that doesn't require instances) see `glide-field-lookup`.
 */
export class ConnectedReference extends React.PureComponent {
	static componentName = 'sn-record-reference-connected';
	mapResults = (data, dictionarySorted) => {
		let results = get(data, 'GlideLayout_Query.referenceDataRetriever', {});
		const totalCount = results.matchesCount;

		return {
			recents: getResults(results.referenceRecentDataList, dictionarySorted),
			options: getResults(results.referenceDataList, dictionarySorted),
			totalCount
		};
	};

	getVariables = inputVal => {
		const {
			disableReferenceQualifier,
			recordSysId,
			fieldName,
			tableName,
			ignoreReferenceQualifier,
			searchCancelable,
			resultLimit,
			dictionary,
			formData
		} = this.props;
		if (recordSysId && fieldName && tableName) {
			const autocompleteOrderBy =
				dictionary &&
				dictionary.attributes &&
				find(get(dictionary, 'attributes', {}), { name: 'ref_ac_order_by' });
			return {
				sys_id: get(formData, 'isNewRecord') ? '-1' : recordSysId,
				field: fieldName,
				table: tableName,
				ignoreRefQual: ignoreReferenceQualifier || disableReferenceQualifier,
				paginationLimit: resultLimit,
				chars: inputVal.length > 0 ? inputVal : '**',
				sortBy: (autocompleteOrderBy && autocompleteOrderBy.value) || '',
				...(searchCancelable
					? { 'x-request-cancelable': this.props.componentId }
					: {})
			};
		}
	};

	setInnerRef = ref => {
		this.inputRef = ref;
	};

	componentWillReceiveProps(nextProps) {
		if (nextProps.focusInput !== this.props.focusInput && this.inputRef) {
			this.inputRef.focus();
		}
	}

	render() {
		const results = get(this.props, 'properties.results');
		const isSearching = get(this.props, 'properties.isSearching');
		const {
			maxSearchMatches,
			searchCancelable,
			searchUsingStartsWith,
			searchOnClick
		} = this.props;

		return (
			<GlideQueryBase
				{...omit(this.props, ['resultLimit'])}
				isSearching={isSearching}
				results={results} // results was created from actionHandler
				mapResults={this.mapResults}
				getVariables={this.getVariables}
				innerRef={this.setInnerRef}
				searchUsingStartsWith={searchUsingStartsWith}
				searchOnClick={searchOnClick}
				searchCancelable={searchCancelable}
				maxSearchMatches={maxSearchMatches}
			/>
		);
	}
}

ConnectedReference.propTypes = {
	...fieldPropTypes,
	/**
	 * Dictionary values and attributes for the field
	 */
	dictionary: PropTypes.object,

	/**
	 * Integer to set the debounce interval for hitting the api when searching reference fields
	 * Defaults to 400ms
	 */
	searchDebounce: PropTypes.number,

	/**
	 * Name of the table the record is listed in
	 */
	referenceTable: PropTypes.any,

	/**
	 * Optional value of the dependent field
	 */
	dependentFieldValue: PropTypes.string,

	/**
	 * Please use `dependentFieldValue` instead
	 */
	dependentValue: PropTypes.string,

	/**
	 * String containing all serialized UI changes
	 */
	serializedChanges: PropTypes.any,

	/**
	 * Boolean to toggle reference qualifiers
	 */
	disableReferenceQualifier: PropTypes.any,
	/**
	 * When true, prevent the user from typing in free-form email addresses.
	 * Instead, an email must be selected from the list of available options.
	 */
	disableEmailFreeformEntry: PropTypes.bool,

	/**
	 * Please use `disableReferenceQualifier` instead
	 */
	ignoreReferenceQualifier: PropTypes.any,

	/**
	 * Integer limit of choice suggestions
	 */
	resultLimit: PropTypes.number,

	/**
	 * Server-side cap of number of results
	 */
	maxSearchMatches: PropTypes.number,

	/**
	 * Value can be anything
	 */
	value: PropTypes.any,

	/**
	 * Callback function triggered when the user presses Enter
	 */
	onEnter: PropTypes.func,

	/**
	 * Flag that switches between multiple and single value reference inputs
	 */
	multiSelection: PropTypes.bool,

	/**
	 * Please use `disableEmailFreeformEntry` instead
	 */
	noEmail: PropTypes.bool,

	/**
	 * Callback to show record
	 */
	referenceAddon: PropTypes.oneOfType([
		PropTypes.func,
		PropTypes.bool,
		PropTypes.string
	]),

	/**
	 * Toggles whether to pass the header to allow new search requests to cancel
	 * search transactions that are still running
	 */
	searchCancelable: PropTypes.bool,

	/**
	 * Flag to Enable "contains" searches for reference autocompleters
	 */

	searchUsingStartsWith: PropTypes.bool,

	/**
	 * Flag to searching when clicking on field
	 */
	searchOnClick: PropTypes.bool,

	/**
	 * Callback to launch reference list modal
	 */
	searchAddon: PropTypes.oneOfType([
		PropTypes.func,
		PropTypes.bool,
		PropTypes.string
	]),

	/**
	 * Flag to hide the reference icon
	 */
	hideReferenceIcon: PropTypes.bool,

	/**
	 * LiveUpdate info
	 */
	liveUpdate: PropTypes.object,

	/**
	 * Flag to show the reference icon in readonly mode
	 */
	referenceAddonReadonly: PropTypes.bool,

	/**
	 * Flag to toggle turning off display value warning
	 */
	disableDisplayValueWarning: PropTypes.bool,

	/**
	 * formData object containing glide form data
	 */
	formData: PropTypes.object,

	/**
	 * displayValue of reference field
	 */
	displayValue: PropTypes.oneOfType([
		PropTypes.string,
		PropTypes.number,
		PropTypes.array
	]),

	/**
	 * Declarative Actions Only Props
	 */
	declarativeActionsProps: PropTypes.object,

	/**
	 * Flag to show when a graphQL request is in progress
	 */
	isSearching: PropTypes.bool,

	/**
	 * results from a graphQL request
	 */
	results: PropTypes.oneOfType([PropTypes.object, PropTypes.array])
};

ConnectedReference.style = GlideQueryBase.style;

ConnectedReference.defaultProps = {
	multiSelection: false,
	disableReferenceQualifier: false,
	ignoreReferenceQualifier: false,
	resultLimit: 15,
	referenceAddon: true,
	searchAddon: true,
	searchOnClick: true,
	searchCancelable: true
};

ConnectedReference.initialState = {
	focusInput: null
};

ConnectedReference.actionHandlers = actionHandlers;

export default withValueChangeCallbacks(
	{ stopBubbling: ['SN_GLIDE_QUERY_BASE'] },
	ConnectedReference
);
